<template>
  <div class="hd-address-warpper">
    <div class="hd-address-title">Shipping Address</div>
    <div class="hd-address-btn">
      <a v-link="{path:'/account/address/edit'}">Add shipping address</a> <span>You have created <b>2</b> shipping address, you can create up to <b>20</b></span>
    </div>
    <div class="hd-address-management-box">
      <div class="hd-address-empty" v-show="addressEmpty">
        <i class="iconfont">&#xe64e;</i>The shopping address is empty, go and add some.
      </div>
      <div class="hd-address-management-item" v-for="item in address" transition="staggered" stagger="100">
        <h3>{{item.name}}</h3>
        <p><span>Name:</span>{{item.name}}</p>
        <p><span>Address:</span>{{item.address}}</p>
        <p><span>Phone:</span>{{item.phone}}</p>
        <p class="hd-address-management-item-action"><a href="">Edit</a><a href="javascript:;"  @click="showDialog($index)">Delete</a></p>
      </div>
    </div>
  </div>
<dialog v-show="dialog.show"  :dialog='dialog' transition="fade" ></dialog>
</template>

<script>
import dialog from '../common/model'
export default {
  data () {
    let self = this
    return {
      address: [ { name: 'Sivan', address: 'Culver City  4114  Sepulveda Blvd  137 W San Baernardino Rd,CA 91723 Amerivan', phone: '(714)841-3999' }, { name: 'Allen', address: 'Culver City  4114  Sepulveda Blvd  137 W San Baernardino Rd,CA 91723 Amerivan', phone: '(714)841-3999' } ],
      dialog: {
        show: false,
        text: 'Are you sure you want to delete this shipping address?',
        title: 'Delete Shipping Address',
        btn: { ok: 'Ok', cancel: 'Cancle' },
        hideFn: function () { self.dialog.show = false },
        activeFn: function () {
          self.dialog.show = false
          self.address.splice(self.delIndex, 1)
          if (self.address.length <= 0) {
            self.addressEmpty = true
          }
        }
      },
      delIndex: '',
      addressEmpty: false
    }
  },
  methods: {
    showDialog (index) {
      this.dialog.show = true
      this.delIndex = index
    }
  },
  events: {
  },
  components: {
    dialog
  }
}
</script>

<style>
.hd-address-title{ height: 30px; line-height: 30px; font-weight: 600; font-size: 1.2em;border-bottom: 1px solid #ddd; }
.hd-address-btn{margin: 30px 0; color: #999;}
.hd-address-btn a{ padding: 8px 20px; background-color: #288dcd; color: #fff; margin-right: 20px; border-radius: 4px;}
.hd-address-btn a:hover{background-color: #0b72ac;}
.hd-address-btn b{color: #0b72ac; margin: 0 2px;}
.hd-address-management-item{ border-radius: 4px; border: 4px solid #e6e6e6; padding: 10px;margin-bottom: 20px;}
.hd-address-management-item p{ line-height: 25px; }
.hd-address-management-item p span{display: inline-block; width: 60px; color: #666;}
.hd-address-management-item p a{ color: #0b72ac;margin-left: 20px; }
.hd-address-management-item p a:hover{ color: #129be7; text-decoration: underline; }
.hd-address-management-item-action{text-align: right;}
</style>


