<template>
<div class="pagination">
  <ul class="pagination-page">
    <li v-if="this.combination.cur>1" ><a v-on:click="combination.cur-- ,pageClick()" href="javascrript:;"><i class="iconfont">&#xe63d;</i></a></li>
    <li v-else class="disabled"><a href="javascrript:;"><i class="iconfont">&#xe63d;</i></a></li>
    <li v-for="index in indexs"  v-bind:class="{'active': combination.cur === index }">
      <a v-on:click="btnClick(index)" href="javascrript:;">{{ index }}</a>
    </li>
    <li v-if="this.combination.cur != this.combination.all" ><a v-on:click="combination.cur++,pageClick()" href="javascrript:;"><i class="iconfont">&#xe63e;</i></a></li>
    <li v-else class="disabled"><a href="javascrript:;"><i class="iconfont">&#xe63e;</i></a></li>
  </ul>
  <!-- <div class="pagination-all">total <i>{{ combination.all }}</i>pages</div> -->
</div>  
</template>

<style>
.pagination {margin-bottom: 5px;  float: right;}
.pagination li{ width: 25px; height: 25px; line-height: 25px; text-align: center; border: 1px solid #ddd; float: left;margin-left: -1px; }
.pagination li:hover{background-color: #f9f9f9;}
.pagination li a{ color: #337ab7; display: block; }
.pagination li:hover{background-color: #eee;}
.pagination li.active{ background-color: #337ab7;border-color:#337ab7;  }
.pagination li.active a{ color: #fff; }
.pagination li.disabled a{color: #999; cursor: not-allowed; background-color: #eee;  }
</style>
<script>
export default {
  props: ['combination'],
  computed: {
    indexs () {
      var left = 1
      var right = this.combination.all
      var ar = []
      if (this.combination.all >= 7) {
        if (this.combination.cur > 5 && this.combination.cur < this.combination.all - 4) {
          left = this.combination.cur - 3
          right = this.combination.cur + 3
        } else {
          if (this.combination.cur <= 5) {
            left = 1
            right = 7
          } else {
            right = this.combination.all
            left = this.combination.all - 6
          }
        }
      }
      while (left <= right) {
        ar.push(left)
        left++
      }
      return ar
    }
  },
  methods: {
    btnClick: function (value) {
      if (value !== this.combination.cur) {
        this.combination.cur = value
      }
      // this.$http.get('/'+this.combination.type+'/page'+this.combination.cur, function (data) {
      //   this.combination = data
      // })
      console.log('' + this.combination.cur + '')
    },
    pageClick: function () {
      console.log('' + this.combination.cur + '')
      // this.$http.get('/'+this.combination.type+'/page'+this.combination.cur, function (data) {
      //   this.combination = data
      // })
    }
  }
}
</script>



