<template>
  <div class="hd-info-faq">
    <div class="hd-info-item" v-for="item in faq">
      <div class="hd-info-item-question">
        <div class="question-short">Q:</div>
        <div class="hd-info-faq-q">{{item.question}}</div>
      </div>
      <div class="hd-info-item-answer">
        <div class="question-short">A:</div>
        <div class="hd-info-faq-a">{{item.answer}}<a href="javascript:;" v-show='showLink' @click='showMore'>+More</a></div>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      faq: [
      { question: 'How long is the warranty period, and is it honored by Hikvision USA?', answer: 'I bought two of these via Amazon from this vendor. The cameras I received lack the HikVision marking, packaging, and associated software. I noticed that the firmware was several versions older than the versions reported by users of this HikVision camera. I called HikVision USA, and learned that they will not provide' },
      { question: 'do i need poe injector if I am planning to use poe adapter? and how would I connect it?', answer: 'Unless you are using just one camera, a POE switch is nice. It provides camera power through the network cable. I\'m using http://www.amazon.com/TRENDnet-8-Port-100Mbps-Switch-TPE-S44/dp/B000QYEN1W/ref=sr_1_1?ie=UTF8&qid=1389139282&sr=8-1&keywords=trendnet+poe+switch The advantages of a POE switch are less wire cl' },
      { question: 'Are these cameras compatible with Synology video servers?', answer: 'Yes. You have to set it up as a PSIA or ONVIF device, but it functions fine. If you want to use motion detection on the camera to save CPU cycles on te Synology, then use PSIA.' },
      { question: 'Is there a PTZ model available?', answer: 'Not of this camera. Hikvision does make PTZ cameras but almost all PTZ camera are vandal resist domes of one flavor or another. They cost a lot of $$$ and you want them protected a bit so someone won\'t trash one by tossing a small stone at it.' } ],
      showLink: 'true',
      subLength: 200
    }
  },
  methods: {
    showMore () {
    }
  }
}
</script>

<style>
.hd-info-faq{margin-top: 20px;}
.hd-info-item{ overflow: hidden; border-bottom: 1px solid #eee; padding: 20px 0 10px 0; }
.hd-info-item-question{ height: 40px; line-height: 40px;}
.question-short{ color: #e5e5e5; font-size: 2.5em; float: left; width: 40px; vertical-align: top; }
.hd-info-faq-q{ height: 40px; overflow: hidden; font-size: 14px;}
.hd-info-faq-a{padding-top: 10px; height:44px ; overflow: hidden;color: #666; word-break: break-all;}
.hd-info-faq-a a{ margin-left: 10px; color: #129be7; }
.hd-info-faq-a a:hover{ text-decoration: underline; }
</style>


