  methods: {
    showBanner (num) {
      this.currentBanner = num
      clearInterval(this.handle)
    },
    stopAuto () {
      clearInterval(this.handle)
    },
    starAuto () {
      let self = this
      this.handle = setInterval(function () {
        if (self.currentBanner < 2) {
          self.currentBanner += 1
        } else {
          self.currentBanner = 0
        }
      }, 5000)
    }
  },

  ready () {
    let self = this
    this.handle = setInterval(function () {
      if (self.currentBanner < 2) {
        self.currentBanner += 1
      } else {
        self.currentBanner = 0
      }
    }, 5000)
    this.$http({ url: '/', method: 'GET' }).then(function (response) {
      console.log(response.data)
      // this.$set('someData', response.data)
    }, function (response) {
      console.log(response.data)
      // error callback
    })
  },
  destroyed () {
    clearInterval(this.handle)
  }
