<template>
<div class="hd-order-warpper">
  <div class="hd-order-title">
    <h3>My Order</h3>
  </div>
  <div class="hd-order-link">
    <div class="hd-order-topbar">
      <a href="javascript:;" :class="{active:current==0}">All Orders</a>
      <a href="javascript:;" :class="{active:current==1}">Completed Orders</a>
      <a href="javascript:;" :class="{active:current==2}">Pending Payment </a>
      <a href="javascript:;" :class="{active:current==3}">Canceled Orders</a>
    </div>
    <div class="hd-order-search">
      <input type="text" name="" id="" placeholder="order No. / product name">
      <a href="javascript:;"><i class="iconfont">&#xe604;</i></a>
    </div>
  </div>
  <div class="hd-order-content">
    <div class="hd-order-item" v-for="item in order">
      <div class="hd-order-item-top">
        <span>{{item.time}}</span><span>Order NO.<b>{{item.no}}</b></span>
      </div>
      <div class="hd-order-item-info">
        <div class="hd-order-item-pic"><img :src="item.src" alt=""></div>
        <div class="hd-order-item-text">
          <p>{{item.title}}</p>
          <p class="hd-order-item-text-model">{{item.model}}</p>
        </div>
        <div class="hd-order-item-num">{{item.num}}</div>
        <div class="hd-order-item-name">{{item.name}}</div>
        <div class="hd-order-item-price"><b>{{item.price}}</b></div>
        <div class="hd-order-item-action">
          <p class="paystate">{{item.pay}}</p>
          <p class="check" v-show="item.pay=='unpaid'"><a v-link="{path:'/buy2'}">Check out</a></p>
          <p class="datalink"><a v-link="{path:'/account/order/info'}">Details</a></p>
        </div>
      </div>
    </div>
  </div>
  <pagination :combination="page"></pagination>
</div>
</template>

<script>
import pagination from '../common/pagination'
export default {
  data () {
    return {
      order: [ { src: require('../../assets/images/info1.jpg'), title: 'TENVIS TH692 Outdoor HD 720P Wireless Waterproof IP/Network Security Camera', model: 'Color : White, Model : SD Card', price: '$59.99', num: 1, time: '2016-4-6 14:12:26', name: 'Allen', pay: 'completed', no: 1575684235 }, { src: require('../../assets/images/new3.jpg'), title: 'HIKVISION DS-2CE16D1T-IT3 2MP Outdoor IR Waterproof Bullet Network ', model: 'Color : Black, Model : SD Card', price: '$85.00', num: 1, time: '2016-4-4 18:14:53', name: 'Sivan', pay: 'unpaid', no: 3654789541 }, { src: require('../../assets/images/new2.jpg'), title: 'HIKVISION DS-2CE16D1T-IT3 2MP Outdoor IR Waterproof Bullet Network ', model: 'Color : Black, Model : SD Card', price: '$85.00', num: 1, time: '2016-4-3 17:45:16', name: 'Sivan', pay: 'cancelled', no: 6721456874 } ],
      current: 0,
      page: { all: 20, cur: 1 }
    }
  },
  methods: {
  },
  components: {
    pagination
  }
}
</script>

<style>
.hd-order-warpper{overflow: hidden;}
.hd-order-title h3{font-size: 1.2em;line-height: 30px; border-bottom: 1px solid #ddd;}
.hd-order-link{ height: 35px;background-color: #eee;margin: 15px 0;overflow: hidden; }
.hd-order-topbar{ float: left; padding: 0 10px; line-height: 35px; }
.hd-order-search{ float: right; }
.hd-order-topbar a{ color: #666;margin-right: 15px; }
.hd-order-topbar a:hover{ color: #0b72ac; }
.hd-order-topbar a.active{ color: #0b72ac; text-decoration: underline; }
.hd-order-search{ height: 25px;margin: 5px;position: relative; }
.hd-order-search input{ height: 25px; padding: 0 30px 0 5px; border: none; width: 220px; font-size: 12px; }
.hd-order-search a{display: block; width: 25px; height: 25px; position: absolute;right: 0;top: 0; text-align: center; color: #999;}
.hd-order-search a i{ font-size: 1.5em; }
.hd-order-search a:hover{ color: #0b72ac; }
.hd-order-item{border: 1px solid #eee; margin-bottom: 25px;}
.hd-order-item-top{ height: 35px; line-height: 35px; background-color: #f5f5f5; color: #666; padding: 0 10px; }
.hd-order-item-top span{ margin-right: 20px; }
.hd-order-item-top span b{ color: #129be7; font-weight: 400; margin-left: 5px; }
.hd-order-item-info{ overflow: hidden; padding: 10px 0; color: #333; }
.hd-order-item-info div{ float: left;}
.hd-order-item-pic,.hd-order-item-num,.hd-order-item-name,.hd-order-item-price,.hd-order-item-action{ width: 14%; text-align: center;  }
.hd-order-item-num,.hd-order-item-name,.hd-order-item-price{padding-top: 30px;}
.hd-order-item-text{ width: 30%;padding-top: 10px;}
.hd-order-item-pic img{ width: 75px; height: 75px; border: 1px solid #eee; }
.hd-order-item-text-model{ color: #999; }
.hd-order-item-price b{color: #da2525;}
.hd-order-item-action{ padding-top: 15px; }
.hd-order-item-action .paystate{ color: #999; }
.hd-order-item-action .check{margin: 5px 0;}
.hd-order-item-action .check a{ background-color: #129be7; color: #fff; border-radius: 4px; padding: 2px 10px; }
.hd-order-item-action .check a:hover{background-color: #0b72ac;}
.hd-order-item-action .datalink a{ color: #129be7; }
.hd-order-item-action .datalink a:hover{ color: #0b72ac; text-decoration: underline; }
</style>


