<template>
<div class="hd-address">
	<div class="hd-address-select">
		<div class="hd-address-select-title">
			<h3>Select a shipping address</h3>
			<p>Is the address you'd like to use displayed below? If so, click the corresponding "Ship to this address" button. Or you can <a href="">enter a new shipping address</a> </p>
		</div>
		<div class="hd-address-empty" v-show="addressEmpty">
	      <i class="iconfont">&#xe64e;</i>The shopping address is empty, go and add some.<a href="">Add shipping address</a>
	    </div>
		<div class="hd-address-box">
			<div class="hd-address-item" v-for="item in addressItem" :class="{active:currentAddress==$index}" @click="currentAddress=$index">
				<p class="hd-address-item-name">{{item.name}}</p>
				<p class="hd-address-item-city">{{item.city}}</p>
				<p class="hd-address-item-line">{{item.line1}}</p>
				<p class="hd-address-item-line">{{item.line2}}</p>
				<p class="hd-address-item-country">{{item.country}}</p>
				<p class="hd-address-item-phone">{{item.phone}}</p>
				<p class="hd-address-item-ac"><a @click.stop="$event.stopPropagation()">Edit</a><a @click.stop="$event.stopPropagation()" @click="showDialog($index)">Delete</a></p>
			</div>
		</div>
	</div>
	<div class="hd-shoppingList">
		<div class="hd-shoppingList-title">
			<h3>Shopping List</h3>
			<a v-link="{path:'/cart'}">Change quantities or delete</a>
		</div>
		<div class="hd-shoppingList-box">
			<div class="hd-shoppingList-item" v-for="item in shoppingList" >
				<div class="hd-shoppingList-item-pic"><img :src="item.src" alt=""></div>
		        <div class="hd-shoppingList-item-info">
		          <p class="hd-shoppingList-item-title">{{item.title}}</p>
		          <p class="hd-shoppingList-item-model">{{item.model}}</p>
		        </div>
		        <div class="hd-shoppingList-item-price">
		          <p>Price: <b>{{item.price}}</b></p>
		        </div>
		        <div class="hd-shoppingList-item-num">
		          <p>{{item.num}}</p>
		        </div>
				<div class="hd-shoppingList-item-fatory">
					<p>{{item.factory}}</p>
				</div>

			</div>
		</div>
	</div>
	<div class="hd-shoppingList-money">
		<p><span class="title">{{totalDate.all}} items:</span><span class="value">${{totalDate.money}}</span></p>
		<p><span class="title">preferntial:</span><span class="value">-${{preferntial.toFixed(2)}}</span></p>
		<p><span class="title">shipment:</span><span class="value">${{shipment.toFixed(2)}}</span></p>
		<p><span class="title">subtotal:</span><span class="value"><b>${{totalDate.money-preferntial+shipment}}</b></span></p>
		<p><a v-link="{path:'/buy2'}" class="hd-shoppingList-money-pay">Payment</a></p>
	</div>

</div>
<dialog v-show="dialog.show"  :dialog='dialog' transition="fade"></dialog>
</template>
<script>
import dialog from '../common/model'

export default {
  props: ['data'],
  data () {
    let self = this
    return {
      addressItem: [ { name: 'Sivan', city: 'Culver City', line1: '4114 Sepulveda Blvd', line2: '137 W San Bermardion Rd CA 91723', country: 'American', phone: '(714)841-3999' }, { name: 'Allen', city: 'Shen Zhen', line1: 'luo hu ai guo lu', line2: 'shui ku xin cun', country: 'China', phone: '(0755)265-4357' } ],
      currentAddress: 0,
      shipment: 15,
      preferntial: 10,
      shoppingList: [ { src: require('../../assets/images/info1.jpg'), title: 'TENVIS TH692 Outdoor HD 720P Wireless Waterproof IP/Network Security Camera', model: 'Color : White, Model : SD Card', price: '$59.99', num: 1, factory: 'TENVIS' }, { src: require('../../assets/images/other7.jpg'), title: 'HDHawk IPC9600 1080P HDMI HD video Monitor IP Camera', model: 'Color : Black, Model : SD Card', price: '$399.99', num: 1, factory: 'Ecasazone' }, { src: require('../../assets/images/new3.jpg'), title: 'HIKVISION DS-2CE16D1T-IT3 2MP Outdoor IR Waterproof Bullet Network ', model: 'Color : Black, Model : SD Card', price: '$85.00', num: 1, factory: 'HDHAWK' } ],
      addressEmpty: false,
      delIndex: '',
      dialog: {
        show: false,
        text: 'Are you sure you want to delete this address?',
        title: 'Delete address',
        btn: { ok: 'Ok', cancel: 'Cancle' },
        hideFn: function () { self.dialog.show = false },
        activeFn: function () {
          self.dialog.show = false
          self.addressItem.splice(self.delIndex, 1)
          if (self.addressItem.length <= 0) {
            self.addressEmpty = true
          }
        }
      }
    }
  },
  methods: {
    showDialog (index) {
      this.dialog.show = true
      this.delIndex = index
    }
  },
  computed: {
    totalDate () {
      var total = 0
      var item = 0
      for (let i = 0; i < this.shoppingList.length; i++) {
        var that = this.shoppingList[i]
        total += that.price.substring(1) * that.num
        item += that.num
      }
      return { money: total, all: item }
    }
  },
  components: {
    dialog
  },
  events: {
  }
}
</script>
<style>
.hd-address{max-width: 1200px; min-width: 1000px; margin: auto;font-size: 12px;}
.hd-address-select{ overflow: hidden; }
.hd-address-select-title{ border-bottom: 1px solid #ddd;}
.hd-address-select-title h3{font-size: 1.5em;font-weight: 500;}
.hd-address-select-title p{ color: #999;line-height: 30px; }
.hd-address-select-title p a{ color: #129be7; margin-left: 5px; }
.hd-address-select-title p a:hover{text-decoration: underline; color: #0a72ac;}
.hd-address-box{ overflow: hidden; margin-top: 40px; }
.hd-address-item{ border:1px solid #ddd; float: left;width: 25%; padding: 10px 0; margin-right: 5%; cursor: pointer; margin-bottom: 10px; }
.hd-address-item.active{ border-color: #ed0808; background: url('../../assets/images/select.png') right bottom no-repeat; }
.hd-address-item p{ padding:2px 10px;}
.hd-address-item-name{font-size: 14px; font-weight: 600; }
.hd-address-item-ac{margin: 5px 0}
.hd-address-item-ac a{ color: #129be7; margin-right: 20px; }
.hd-address-item-ac a:hover{ text-decoration: underline;color: #0a72ac;}
.hd-shoppingList{margin-top: 40px;}
.hd-shoppingList-title{ position: relative;border-bottom: 1px solid #ddd;padding-bottom: 10px; }
.hd-shoppingList-title h3{font-size: 1.5em;font-weight: 500;}
.hd-shoppingList-title a{ position: absolute; right: 0; bottom: 5px; color: #129be7;}
.hd-shoppingList-title a:hover{ text-decoration: underline;color: #0a72ac;}
.hd-shoppingList-box{ margin: 20px 0; }
.hd-shoppingList-item{ background-color: #edfafe; padding: 20px 0; overflow: hidden;margin-bottom: 20px;}
.hd-shoppingList-item-pic{float: left; width: 15%; }
.hd-shoppingList-item-pic img{ width: 90px; height: 90px; margin-left: 20px; }
.hd-shoppingList-item-info{ float: left;width: 40%; padding-top: 30px; }
.hd-shoppingList-item-title{ max-height: 34px; overflow: hidden; }
.hd-shoppingList-item-model{ color: #999; }
.hd-shoppingList-item-price,.hd-shoppingList-item-num,.hd-shoppingList-item-fatory{ float: left; width: 15%;padding-top: 40px; }
.hd-shoppingList-item-price b{font-weight: 400; color: #da2525;}
.hd-shoppingList-money{border-top: 1px solid #ddd; text-align: right; padding-top: 15px; overflow: hidden;}
.hd-shoppingList-money p{ line-height: 30px;font-size: 14px; }
.hd-shoppingList-money span{display: inline-block;}
.hd-shoppingList-money span b{ color: #e2383b; }
.hd-shoppingList-money .title{ width: 150px; text-align: right;}
.hd-shoppingList-money .value{min-width: 60px; padding-left: 20px;}
.hd-shoppingList-money-pay{display: block; width: 180px; height: 40px; line-height: 40px; text-align: center; color: #fff; border-radius: 5px; background-color: #e2383b; float: right; margin-top: 10px;}
.hd-shoppingList-money-pay:hover{background-color: #c33033;}
.hd-address-empty{ height: 60px; line-height: 60px; color: #333; text-align: center; border:1px solid #ddd; margin: 20px 0; background-color: #f9f9f9; }
.hd-address-empty a{display: inline-block; height: 25px;line-height: 25px; margin-left: 10px; padding: 0 20px; color: #fff; background-color: #e33b3e; border-radius: 5px;}
.hd-address-empty a:hover{background-color: #cd2528;}
</style>


