<template>
  <div class="hd-list-content-title"><i class="iconfont">&#xe607;</i>IP Camera</div>
  <div class="hd-list-content-box">
    <ul class="fn-clear">
      <li class="hd-product-info" v-for="item in products"><a v-link="{path:'/info'}">
        <img :src="item.src" alt="">
        <p class="hd-product-info-text">{{item.text}}</p>
        <p class="hd-product-info-price"><b>{{item.price}}</b></p>
      </a></li>
    </ul>
  </div>
  <pagination :combination="page"></pagination>
</template>

<script>
import pagination from '../common/pagination'
export default {
  data () {
    return {
      products: [
      {src: require('../../assets/images/f1.jpg'), text: 'HIKVISION 3MP IR PoE HD Bullet Network/Wireless WIFI Surveillance Camera', price: '$79.99'},
      {src: require('../../assets/images/f2.jpg'), text: 'HIKVISION DS-2CD2T45-I8 4MP H.265 PoE Outdoor Bullet Network Surveillance Camera', price: '$85.00'},
      {src: require('../../assets/images/f5.jpg'), text: 'IKVISION DS-2CD2145F-IS 4MP PoE Vandal-proof Dome Network Surveillance Camera', price: '$125.99'},
      {src: require('../../assets/images/item2.png'), text: 'TENVIS TZ100 HD Wireless IP/Network Security Camera', price: '$63.99'},
      {src: require('../../assets/images/new5.jpg'), text: 'TENVIS IP391W-HD Outdoor Wireless Waterproof Bullet IP/Network Security Surveillance Camera', price: '$65.99'},
      {src: require('../../assets/images/item1.png'), text: 'TENVIS IP602W Outdoor Wireless Waterproof Bullet IP/Network Security Surveillance Camera', price: '$79.00'},
      {src: require('../../assets/images/hot4.jpg'), text: 'TH661- Wi-Fi HD P2P Pan/Tilt Smart IP Camera ', price: '$69.00'},
      {src: require('../../assets/images/new1.jpg'), text: 'JPT3815W - Pan/Tilt Wireless IP Camera Night Vision - Black', price: '$64.99'},
      {src: require('../../assets/images/new2.jpg'), text: 'IPROBOT3 - HD P2P Megapixel IP Camera - Silver', price: '$63.00'},
      {src: require('../../assets/images/new3.jpg'), text: 'TH692- Outdoor P2P HD Waterproof IP Camera', price: '$110.00'}
      ],
      page: { all: 3, cur: 1 }
    }
  },
  methods: {
  },
  components: {
    pagination
  }
}
</script>
<style>
.hd-list-content-title{ height: 30px;line-height: 30px; padding: 0 5px; background-color: #eee; }
.hd-list-content-box{margin:20px 0;}
.hd-product-info{width: 25%; float: left; text-align: center; }
.hd-product-info a{display: block;padding: 10px 0; border: 1px solid #eee; margin-left: -1px; margin-bottom: -1px;}
.hd-product-info a:hover p{color: #31a2e4;}
.hd-product-info img{ height: 200px; width: 200px; }
.hd-product-info-text{height: 51px; overflow: hidden; padding: 0 10px; word-break: break-all; color: #666;}
.hd-product-info-price{padding:5px 0 10px 0;}
.hd-product-info-price b{color: #da2525;}
.hd-page{clear: both; float: right; height: 30px; color: #666;margin-top: 20px;}
.hd-page-left{margin-right: 20px;}
.hd-page-right a{ display: inline-block; width: 30px; height: 25px; text-align: center;border:1px solid #eee; color: #666; }
.hd-page-right a:hover{ color: #0a72ac; }
.hd-page-right a.disabled{ cursor: not-allowed; color: #999; background-color: #eee; }
</style>


