<template>
  <div class="hd-personal-warpper">
    <div class="hd-personal-title">Personal information</div>
    <div class="hd-personal-info-box">
      <div class="hd-personal-sidebar">
        <a href="javascript:;" @click="current=0" :class="{active:current==0}"> Basic Information</a>
        <a href="javascript:;" @click="current=1" :class="{active:current==1}"> Change Account Password</a>
      </div>
      <div class="hd-personal-basic" v-show="current==0">
        <div class="hd-personal-basic-item">
          <label for="name" class="title"><em>*</em>Full Name</label>
          <input type="text" name="" id="name"  v-model="name">
        </div>
        <div class="hd-personal-basic-item">
          <label for="email" class="title"><em>*</em>Email</label>
          <input type="text" name="" id="email"  v-model="email">
        </div>
        <div class="hd-personal-basic-item">
          <label for="" class="title"><em>*</em>Sex</label>
          <span><input type="radio" id="male" value="male" v-model="sex"><label for="male">Male</label></span>
          <span><input type="radio" id="female" value="female" v-model="sex"><label for="female">Female</label></span>
          <span><input type="radio" id="secrecy" value="secrecy" v-model="sex"><label for="secrecy">Secrecy</label></span>
        </div>
        <div class="hd-personal-basic-item">
          <label for="phone" class="title"><em>*</em>Phone</label>
          <input type="text" name="" id="phone"  v-model="phone">
        </div>
        <div class="hd-personal-basic-item">
          <label for="phone" class="title"></label>
          <button type="button" class="hd-personal-btn">Save</button>
        </div>
      </div>
      <div class="hd-personal-password" v-show="current==1">
        <div class="hd-personal-password-tips">
          Password must be 6-30 characters and condition at least two of the following:uppercase letters,lowercase letters,numbers.and sysbols(e.g.~!@#%_|/*)
        </div>
        <div class="hd-personal-basic-item">
          <label for="passord" class="title"><em>*</em>Current Password</label>
          <input type="passord" name="" id="passord"  v-model="passord">
        </div>
        <div class="hd-personal-basic-item">
          <label for="newpassord" class="title"><em>*</em>New Password</label>
          <input type="passord" name="" id="newpassord"  v-model="newpassord">
        </div>
        <div class="hd-personal-basic-item">
          <label for="confirmnewpassord" class="title"><em>*</em>Confirm New Password</label>
          <input type="passord" name="" id="confirmnewpassord"  v-model="confirmnewpassord" >
        </div>
        <div class="hd-personal-basic-item">
          <label for="phone" class="title"></label>
          <button type="button" class="hd-personal-btn">Save</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      current: 0,
      name: 'sivan',
      email: 'username@hdhawk.com',
      sex: 'secrecy',
      phone: '(714)36547892'

    }
  },
  methods: {
  }
}
</script>

<style>
.hd-personal-title{height: 30px; line-height: 30px; border-bottom: 1px solid #ddd; font-size: 1.2em; font-weight: 600;}
.hd-personal-info-box{margin: 20px 0;}
.hd-personal-sidebar{ height: 30px; line-height: 30px;margin-bottom: 20px; }
.hd-personal-sidebar a{ color: #666;margin-right: 30px; padding: 8px 20px;background-color: #f9f9f9; }
.hd-personal-sidebar a.active{ color: #129be7; background-color: #eee; }
.hd-personal-basic,.hd-personal-password{border-top: 1px solid #eee; padding: 20px 0;}
.hd-personal-basic-item {margin-bottom: 20px;}
.hd-personal-basic-item .title{ display:inline-block; width: 150px; text-align: right;padding-right: 20px;  }
.hd-personal-basic-item .title em{color: #e30c0c;}
.hd-personal-basic-item  input[type='text'],.hd-personal-basic-item  input[type='passord']{ padding: 5px;border: none; width: 300px; border-bottom: 1px solid #ddd; }
.hd-personal-btn{ background-color: #0b72ac; color: #fff; padding: 8px 40px; border-radius: 4px; border: none; cursor: pointer;margin-top: 20px; }
.hd-personal-btn:hover{background-color: #129be7;}
.hd-personal-password-tips{ line-height: 25px; color: #999;margin-bottom: 10px; }
</style>


