<template>
<div class="hd-order-info">
  <div class="hd-order-bread">
    <a v-link="{path:'/account/order'}">My Order</a>/<span>Order Information</span>
  </div>
  <div class="hd-order-back"><a v-link="{path:'/account/order'}">&lt;&lt; Back</a></div>
  <div class="hd-order-status">
    <p class="hd-order-status-no"><span class="no">Order number:<b>{{orderInfo.no}}</b></span><span class="status">Status:<b>{{orderInfo.status}}</b></span><span class="time">Time:<b>{{orderInfo.time}}</b></span></p>
    <p class="hd-order-status-payment"><a v-link="{path:'/buy2'}" v-if="orderInfo.status=='unpaid'">Pay Now</a><span  v-if="orderInfo.status=='completed'">Order has been completed, thank you for shopping at hdhawk.</span></p>
  </div>
  <div class="hd-order-products">
    <div class="hd-order-products-title">Product Listing</div>
    <div class="hd-order-products-box">
      <div class="hd-order-products-item" v-for="item in orderInfo.product">
        <div class="hd-order-products-item-title">{{item.version}}</div>
        <div class="hd-order-products-item-info">
          <div class="hd-order-products-item-pic"><img :src="item.src" alt=""></div>
          <div class="hd-order-products-item-text"><p>{{item.title}}</p><p class="model">{{item.model}}</p></div>
          <div class="hd-order-products-item-num">{{item.num}}</div>
          <div class="hd-order-products-item-price">{{item.price}}</div>
          <div class="hd-order-products-item-factory">{{item.factory}}</div>
          <div class="hd-order-products-item-time">{{item.time}}</div>
        </div>
      </div>
    </div>
  </div>
  <div class="hd-order-recipient">
    <div class="hd-order-recipient-title">Recipient Information</div>
    <div class="hd-order-recipient-info">
      <p><span>Name:</span>{{infomation.name}}</p>
      <p><span>Address:</span>{{infomation.address}}</p>
      <p><span>Phone:</span>{{infomation.phone}}</p>
    </div>
  </div>
  <div class="hd-order-info-total">
    <p class="hd-order-info-total-num"><span>{{orderInfo.product.length}} items:</span><b>${{totalPrice}}</b></p>
    <p class="hd-order-info-total-preferential"><span>Preferential:</span><b>-${{orderInfo.discount.toFixed(2)}}</b></p>
    <p class="hd-order-info-total-shipment"><span>Shipment:</span><b>${{orderInfo.shipment.toFixed(2)}}</b></p>
    <div class="hd-order-info-total-price"><span>Subtotal:</span><b>${{totalPrice-orderInfo.discount+orderInfo.shipment}}</b></div>
  </div>
</div>
</template>

<script>
export default {
  data () {
    return {
      orderInfo: { no: 1657485574, status: 'unpaid', time: '2016-4-4 18:14:53', discount: 10, shipment: 15, product: [ { src: require('../../assets/images/info1.jpg'), title: 'TENVIS TH692 Outdoor HD 720P Wireless Waterproof IP/Network Security Camera', model: 'Color : White, Model : SD Card', price: 59.99, num: 1, factory: 'TENVIS', version: 'TENVIS TH692 Outdoor HD' }, { src: require('../../assets/images/new3.jpg'), title: 'HIKVISION DS-2CE16D1T-IT3 2MP Outdoor IR Waterproof Bullet Network ', model: 'Color : Black, Model : SD Card', price: 85.00, num: 1, factory: 'HDHawk', version: 'HIKVISION DS-2CE16D1T-IT3 2MP' } ] },
      infomation: { name: 'Sivan', address: 'Culver City  4114  Sepulveda Blvd  137 W San Baernardino Rd,CA 91723 Amerivan', phone: '(714)841-3999 ' }
    }
  },
  methods: {
  },
  computed: {
    totalPrice () {
      var total = 0
      for (let i = 0; i < this.orderInfo.product.length; i++) {
        var item = this.orderInfo.product[i]
        total += item.price
      }
      return total.toFixed(2)
    }
  }
}
</script>

<style>
.hd-order-bread{ line-height: 30px;border-bottom: 1px solid #ddd; color: #999; }
.hd-order-bread a{ margin-right: 5px; color: #333; }
.hd-order-bread a:hover{ color: #0b72ac; }
.hd-order-bread span{margin-left: 5px;}
.hd-order-status{ background-color: #f4f9fc; padding: 5px 10px; border: 1px solid #bedef1;margin: 20px 0; color: #666; }
.hd-order-status-no { height: 30px; line-height: 30px; border-bottom: 1px dotted #bedef1; }
.hd-order-status span{ margin-right: 50px; }
.hd-order-status b{ font-weight: 400; margin-left: 5px; }
.hd-order-status .no b{color: #129be7; }
.hd-order-status .status b{color: #da2525;}
.hd-order-status .time b{ color: #129be7; }
.hd-order-status-payment{height: 25px; padding-top: 15px;}
.hd-order-status-payment a{background-color: #3bb020; color: #fff; border-radius: 4px; padding: 4px 20px;}
.hd-order-status-payment a:hover{background-color: #32941c;}
.hd-order-status-payment span{ color: #999; }
.hd-order-products-title{ font-size: 14px; font-weight: 600;height: 30px; line-height: 30px; border-bottom: 1px solid #ddd;margin-bottom: 10px; }
.hd-order-products-item{border: 1px solid #ddd;margin-bottom: 20px;}
.hd-order-products-item-title{ height: 35px; line-height: 35px; background-color: #f5f5f5; padding-left: 10px; color: #999; }
.hd-order-products-item-info{ overflow: hidden; padding: 10px; }
.hd-order-products-item-info div{ float: left; }
.hd-order-products-item-pic{ width: 15%;}
.hd-order-products-item-pic img{ width: 75px; height: 75px; border: 1px solid #eee; }
.hd-order-products-item-num,.hd-order-products-item-price,.hd-order-products-item-factory{ width: 15%;text-align: center; padding-top: 30px;  }
.hd-order-products-item-text{ width: 40%; padding-top: 10px; }
.hd-order-products-item-text .model{ color: #999; }
.hd-order-products-item-price{ color: #da2525; font-weight: 600; }
.hd-order-recipient-title{ font-size: 14px; font-weight: 600;height: 30px; line-height: 30px; border-bottom: 1px solid #ddd;margin-bottom: 10px;  }
.hd-order-recipient-info p{line-height: 25px;}
.hd-order-recipient-info p span{ display: inline-block; width: 80px; color: #666;}
.hd-order-info-total{ margin: 30px 0; background-color: #f5f5f5; padding: 10px 20px; font-size: 14px; overflow: hidden;}
.hd-order-info-total p{ height: 25px; line-height: 25px; text-align: right; }
.hd-order-info-total p span{}
.hd-order-info-total p b{display: inline-block;min-width:70px; padding-left: 20px;font-weight:400 ; }
.hd-order-info-total-price{ text-align: right;  float: right; height: 40px; line-height: 40px;margin-top: 10px; border-top: 1px solid #999;min-width: 200px;}
.hd-order-info-total-price b{ color: #da2525; display: inline-block; min-width: 70px; padding-left: 20px; }
.hd-order-back {margin-top: 20px; }
.hd-order-back a{ padding: 5px 20px; border: 1px solid #ddd; color: #666;background-color: #f9f9f9; }
.hd-order-back a:hover{ background-color: #eee; color:#129be7;}
</style>


