<template>
<div class="hd-cart-warpper">
  <div class="hd-login-prompt">
    <i class="iconfont">&#xe64a;</i>You are not logged in! After logging in the cart so items will be saved to your account in <a v-link="{path:'/login'}">Log In</a>
  </div>
  <div class="hd-cart-box">
    <div class="hd-cart-title">
      <h3><i class="iconfont">&#xe600;</i> Shopping Cart</h3>
    </div>
    <div class="hd-cart-empty" v-show="cartEmpty">
      <i class="iconfont">&#xe615;</i>The shopping cart is empty, go and buy some.<a v-link="{path:'/product'}">Go Shopping</a>
    </div>
    <div class="hd-cart-cnt">
      <div class="hd-cart-item" v-for="item in cartDate" transition="staggered" stagger="100" >
        <div class="hd-cart-item-pic"><img :src="item.src" alt=""></div>
        <div class="hd-cart-item-info">
          <p>{{item.title}}</p>
          <p class="hd-cart-item-info-model">{{item.model}}</p>
        </div>
        <div class="hd-cart-item-price">
          Price: <b>{{item.price}}</b>
        </div>
        <div class="hd-cart-item-num">
          <p><a href="javascript:" @click="minus(item)" class="hd-cart-item-num-minus">-</a><span>{{item.num}}</span><a href="javascript:;" @click="plus(item)" class="hd-cart-item-num-plus">+</a></p>
        </div>
        <div class="hd-cart-item-total" >
          Total: <b>${{ (item.price.substring(1) * item.num).toFixed(2) }}</b>
        </div>
        <div class="hd-cart-item-stock" >
          <p :class="{active:item.state=='in stock'}">{{item.state}}</p>
        </div>
        <div class="hd-cart-item-action">
          <a href="javascript:;" @click="showDialog($index)"><i class="iconfont">&#xe64b;</i>Delete</a>
        </div>
      </div>
    </div>
    <div class="hd-cart-money" v-show="!cartEmpty">
      <p class="hd-cart-money-total">Subtotal ( {{allItem}} items ): <b>${{totalPrice}}</b></p>
      <p class="hd-cart-money-check"><button type="button" v-link="{path:'/buy1'}">Check Out</button></p>
    </div>
  </div>
</div>
<dialog v-show="dialog.show"  :dialog='dialog' transition="fade"></dialog>
</template>

<script>
import dialog from '../common/model'

export default {
  data () {
    let self = this
    return {
      cartDate: [ { src: require('../../assets/images/info1.jpg'), title: 'TENVIS TH692 Outdoor HD 720P Wireless Waterproof IP/Network Security Camera', model: 'Color : White, Model : SD Card', price: '$59.99', num: 1, state: 'in stock' }, { src: require('../../assets/images/other7.jpg'), title: 'HDHawk IPC9600 1080P HDMI HD video Monitor IP Camera', model: 'Color : Black, Model : SD Card', price: '$399.99', num: 1, state: 'out of stock' }, { src: require('../../assets/images/new3.jpg'), title: 'HIKVISION DS-2CE16D1T-IT3 2MP Outdoor IR Waterproof Bullet Network ', model: 'Color : Black, Model : SD Card', price: '$85.00', num: 1, state: 'in stock' } ],
      dialog: {
        show: false,
        text: 'Are you sure you want to delete this item?',
        title: 'Delete prompt',
        btn: { ok: 'Ok', cancel: 'Cancle' },
        hideFn: function () { self.dialog.show = false },
        activeFn: function () {
          self.dialog.show = false
          self.cartDate.splice(self.delIndex, 1)
          if (self.cartDate.length <= 0) {
            self.cartEmpty = true
          }
        }
      },
      delIndex: '',
      cartEmpty: false
    }
  },
  methods: {
    minus (item) {
      if (item.num >= 1) {
        item.num -= 1
      }
    },
    plus (item) {
      item.num += 1
    },
    showDialog (index) {
      this.dialog.show = true
      this.delIndex = index
    }
  },
  computed: {
    totalPrice () {
      var total = 0
      for (let i = 0; i < this.cartDate.length; i++) {
        var item = this.cartDate[i]
        total += item.price.substring(1) * item.num
      }
      return total.toFixed(2)
    },
    allItem () {
      var allItem = 0
      for (let i = 0; i < this.cartDate.length; i++) {
        var item = this.cartDate[i]
        allItem += item.num
      }
      return allItem
    }
  },
  events: {
  },
  components: {
    dialog
  }
}
</script>
<style>
.hd-cart-warpper{max-width: 1200px; min-width: 1000px; margin: auto;font-size: 12px;}
.hd-login-prompt{height: 45px;line-height: 45px; margin: 20px 0; border:1px solid #edd28b; background-color: #fffdee; color: #ff8223; text-align: center;}
.hd-login-prompt i { color: #ffcc00; }
.hd-login-prompt a{display: inline-block; height: 25px;line-height: 25px; margin-left: 10px; padding: 0 30px; color: #fff; background-color: #e33b3e; border-radius: 5px; }
.hd-login-prompt a:hover{background-color: #cd2528;}
.hd-cart-title{font-size: 14px; border-bottom: 2px solid #999; line-height: 40px;}
.hd-cart-item{background-color: #edfafe;margin: 20px 0; padding: 10px 0; overflow: hidden;}
.hd-cart-item-pic{ width: 11%; float: left;}
.hd-cart-item-pic img{ width: 90px; height: 90px; border:1px solid #eee;margin-left: 10px;  }
.hd-cart-item-info{ float: left; width: 34%;padding-top: 20px;}
.hd-cart-item-info-model{ color: #999; }
.hd-cart-item-price,.hd-cart-item-num,.hd-cart-item-total,.hd-cart-item-stock,.hd-cart-item-action{ width: 11%; float: left; text-align: center;}
.hd-cart-item-price,.hd-cart-item-total,.hd-cart-item-stock,.hd-cart-item-action{padding-top: 40px; }
.hd-cart-item-price b{color: #da2525;font-weight: 400; }
.hd-cart-item-stock{ color: #999; }
.hd-cart-item-stock p.active{ color: #37c047; }
.hd-cart-item-total b{color: #da2525;font-weight: 600; }
.hd-cart-item-num p{ height: 25px;line-height: 25px; border:1px solid #ddd; background-color: #fff;margin-top: 35px;position: relative; }
.hd-cart-item-num p a{display: inline-block; width: 20%; color: #000;}
.hd-cart-item-num-minus{ float: left; border-right: 1px solid #ddd; } 
.hd-cart-item-num-plus{ float: right;border-left: 1px solid #ddd; }
.hd-cart-item-action a{ color: #268bc4; }
.hd-cart-item-action a:hover{ color: #05476b;}
.hd-cart-empty{ height: 60px; line-height: 60px; color: #333; text-align: center; border:1px solid #ddd; margin: 20px 0; background-color: #f9f9f9; }
.hd-cart-empty a{display: inline-block; height: 25px;line-height: 25px; margin-left: 10px; padding: 0 20px; color: #fff; background-color: #e33b3e; border-radius: 5px;}
.hd-cart-empty a:hover{background-color: #cd2528;}
.hd-cart-money{border-top: 1px solid #ddd; margin: 30px 0; text-align: right;line-height: 40px;}
.hd-cart-money-total{ font-size: 14px; }
.hd-cart-money-total b{margin-left: 10px;font-size: 1.5em; color: #da2525; font-weight: 600;}
.hd-cart-money-check { margin-top: 10px; }
.hd-cart-money-check button{height: 40px; width: 200px;color: #fff; background-color: #e33b3e; border:none; border-radius: 5px; cursor: pointer;}
.hd-cart-money-check button:hover{background-color: #cd2528;}
</style>


