<style>
.hd-info-tech img{ max-width: 1200px;margin-top: 20px; }
.hd-loading-cnt{ padding: 10px; font-size: 12px; color: #666; color: #ff8080; }
.hd-loading-cnt i{ margin-right: 10px; font-size: 1.5em; }
</style>


