<template>
  <div id='warpper'>
    <app-header></app-header>
    <app-menu></app-menu>
    <div class="hd-register-suc">
      <p class="hd-register-suc-title"><i class="iconfont">&#xe624;</i>Congratulation,you have registered successfully!</p>
      <p class="hd-register-suc-tips">Your account is: <b>username@tenvis.com</b>. Welcome to HDHawk, we hope you enjoy shopping with us ! Please wait while we redirect you back to the previous page.</p>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
import appHeader from '../common/appHeader'
import appMenu from '../common/appMenu'
import appFooter from '../common/appFooter'

export default {
  components: {
    appHeader,
    appMenu,
    appFooter
  },
  ready () {
    // console.log(this.$route.path)
    var self = this
    setTimeout(function () {
      self.$route.router.go({path: '/'})
    }, 5000)
  }
}
</script>
<style>
.hd-register-suc{max-width: 1200px;min-width: 1000px;margin: 50px auto 200px; padding: 30px 0; border-radius: 5px; text-align: center;background-color: #ebfbe1; border: 1px solid #d7efca;}
.hd-register-suc p{ width: 500px; margin: auto;}
.hd-register-suc-title{color: #339f04;}
.hd-register-suc-title i{font-size: 2em; margin-right: 10px;} 
.hd-register-suc-tips{font-size: 12px; color: #999; text-align: left; padding: 5px 0 10px 200px;}
.hd-register-suc-tips b{ color: #0a72ac;font-weight: 400; }
</style>


