<template>
<div class="hd-support">
  <div class="hd-support-sidebar">
    <ul>
      <li v-for="item in sideMenu"  transition="staggered" stagger="100" >
        <a href="javascript:;" class="title" :class="{active:current == $index}" @click="current=$index">{{item.first}}<i class="iconfont">&#xe620;</i></a>
        <ul v-show="current==$index" transition="slideDown">
          <li v-for="i in item.second"  transition="staggered" stagger="100" @click="showMenu (item, $index)"><a v-link="{path:'/support/list'}" :class="{active:activeMenu == i }" @click="activeMenu = i" >{{i}}</a></li>
        </ul>
      </li>
    </ul>
  </div>
  <div class="hd-support-content">
    <router-view transition="fade" :bread="bread" :faq="faq" ></router-view>
  </div>
</div>
</template>

<script>
export default {
  data () {
    return {
      sideMenu: [ { first: 'Shopping Guide', second: [ 'Terms of the transaction', 'Shopping process', 'Account registration' ] }, { first: 'Payment', second: [ 'Payment process', 'Payment card' ] }, { first: 'Delivery', second: [ 'Delivery process', 'Delivery time', 'Delivery freight' ] }, { first: 'Service', second: [ 'Return guarantee', 'Warranty period', 'Service Guarantee' ] }, { first: 'Contact', second: [ 'Contact information', 'Online service', 'Common problem' ] } ],
      // sideMenu: [ { first: 'Shopping Guide', second: [ { text: 'Terms of the transaction', list: [ 'Register an account', 'Order Process' ] }, { text: 'Shopping process', list: [ 'Selection of merchandise', 'Submit Order' ] }, { text: 'Account registration', list: [ 'Selection of merchandise', 'Submit Order' ] } ] }, { first: 'Payment', second: [ { text: 'Shopping process', list: [ 'Selection of merchandise', 'Submit Order' ] }, { text: 'Account registration', list: [ 'Selection of merchandise', 'Submit Order' ] } ] }, { first: 'Delivery', second: [ { text: 'Shopping process', list: [ 'Selection of merchandise', 'Submit Order' ] }, { text: 'Account registration', list: [ 'Selection of merchandise', 'Submit Order' ] } ] }, { first: 'Service', second: [ { text: 'Shopping process', list: [ 'Selection of merchandise', 'Submit Order' ] }, { text: 'Account registration', list: [ 'Selection of merchandise', 'Submit Order' ] } ] }, { first: 'Contact', second: [ { text: 'Shopping process', list: [ 'Selection of merchandise', 'Submit Order' ] }, { text: 'Account registration', list: [ 'Selection of merchandise', 'Submit Order' ] } ] } ],
      // topmenu: [ 'Register an account', 'Order Process', 'Selection of merchandise', 'Submit Order' ],
      faq: [ 'How to register account?', 'How to register member companies?', 'HDHawk Shopping Process', 'How to purchase orders?', 'How to find the desired product?', 'You can call the customer service hotline to order merchandise it?', 'How to fill out the shipping address?', 'How to View the shopping cart of goods?', 'Why there is no delivery address my region where the streets?', 'How to open a browser cookie functionality?', ' Shopping Cart FAQ and shopping tips', 'Beware of scams under the guise of Jingdong' ],
      activeMenu: 'Terms of the transaction',
      current: 0,
      bread: { first: 'Shopping Guide', second: 'Terms of the transaction', list: [ 'Register an account', 'Order Process' ] }

    }
  },
  methods: {
    showMenu (item, index) {
      this.bread.first = item.first
      this.bread.second = item.second[index]
      // this.bread.list = item.second[index].list
    }
  }
}
</script>

<style>
.hd-support{max-width: 1200px; min-width: 1000px; margin:20px auto;min-height: 800px;position: relative;font-size: 12px;}
.hd-support-sidebar{ position: absolute; left: 0; top: 0; width: 220px; height: 100%; border-right: 1px solid #eee; }
.hd-support-sidebar h3{ height: 40px; line-height: 40px; text-align: center; color: #fff; font-weight: 400; background-color: #0b72ac; }
.hd-support-sidebar li{line-height: 40px; }
.hd-support-sidebar li .title{display: block; height: 35px; line-height: 35px; position: relative; background-color: #fafafa; padding-left: 35px; color: #555; font-weight: 600; border-bottom: 1px solid #eee;}
.hd-support-sidebar li .title:hover{background-color: #eee;}
.hd-support-sidebar li .title i{ font-size: 0.8em; position: absolute; right: 10px; color: #aaa; }
.hd-support-sidebar li .title.active{background-color: #ddd;}
.hd-support-content{margin-left: 250px;}
.hd-support-sidebar li li{border-bottom: 1px dotted #eee;}
.hd-support-sidebar li li a{ display: block; padding-left: 35px; color: #666; }
.hd-support-sidebar li li a:hover{ color: #000;}
.hd-support-sidebar li li a.active{ color: #129be7;}
</style>


