<template>
  <div class="hd-info-warpper">
    <div class="hd-info-product">
      <div class="hd-info-pic">
        <img :src="imgsrc" alt="" class="hd-info-pic-max">
        <div class="hd-info-pic-min">
          <ul>
            <li  v-for="item in photos"><a href="javascript:;"><img :src="item" alt="" @click='changeImg($event,$index)' :class="{active:activeImg==$index}" ></a></li>
          </ul>
        </div>
      </div>
      <div class="hd-info-text">
        <div class="hd-info-text-data">
          <h1 class="hd-info-text-title">{{productInfo.title}}</h1>
          <p class="hd-info-text-label">{{productInfo.text}}</p>
          <p class="hd-info-text-price">{{productInfo.price}}</p>
        </div>
        <div class="hd-info-text-attribute">
          <dl class="hd-info-text-item" v-for="item in attribute">
            <dt>{{item.title}} :</dt>
            <dd v-for="i in item.options" @click='getSelected(i,$index,item)' :class="{active:item.state==$index}" >{{i}}</dd>
          </dl>
        </div>
        <div class="hd-info-text-total">
          <p>You have chosen the following product:</p>
          <p class="hd-info-total-detail">TENVIS TH692 HD Outdoor  720P IP Camera -- {{Color}} -- {{Model}}</p>
          <p class="hd-info-total-price">Total: <b>{{productInfo.price}}</b></p>
        </div>
        <div class="hd-info-buy">
          <button class="hd-info-buy-btn" v-link="{path:'/buy1'}"><i class="iconfont">&#xe600;</i>Add to Cart</button> <span>stock: {{productInfo.stock}}</span>
        </div>

      </div>
    </div>
    <div class="hd-info-tab">
      <div class="hd-info-tab-link">
        <a v-link="{path:'/info/design',activeClass: 'active'}" ><i class="iconfont">&#xe612;</i>Design</a>
        <a v-link="{path:'/info/tech',activeClass: 'active'}" ><i class="iconfont">&#xe616;</i>Tech Specs</a>
        <a v-link="{path:'/info/faq',activeClass: 'active'}" ><i class="iconfont">&#xe614;</i>FAQ</a>
      </div>
      <div class="hd-info-tab-content">
        <router-view transition="fade" ></router-view>
        <!-- <component :is="currentView" keep-alive></component> -->
      </div>
    </div>
  </div>
</template>

<script>
import tech from './tech'
import faq from './faq'
import design from './design'
export default {
  data () {
    return {
      productInfo: { title: 'TENVIS TH692 Outdoor HD 720P IP Camera', text: 'Wireless Waterproof IP/Network Security Camera, Remote Live View, Capture Picture And Video Clip, Plug&Play, With Up To 15m Night Vision...', price: '$59.99', stock: '100' },
      photos: [ require('../../assets/images/info1.jpg'), require('../../assets/images/new1.jpg'), require('../../assets/images/new2.jpg') ],
      imgsrc: require('../../assets/images/info1.jpg'),
      attribute: [ { title: 'Color', options: [ 'White', 'Black' ], state: '3' }, { title: 'Model', options: [ 'SD Card', 'NO SD Card' ], state: '3' } ],
      activeImg: 0,
      current: 0,
      Color: '',
      Model: '',
      currentView: 'faq'

    }
  },
  methods: {
    changeImg (e, num) {
      this.imgsrc = e.target.src
      this.activeImg = num
    },
    getSelected (i, index, item) {
      item.state = index
      this[item.title] = i
    }
  },
  components: {
    tech,
    faq,
    design
  }
}
</script>

<style>
.hd-info-warpper{max-width: 1200px; min-width: 1000px;margin:20px auto;font-size: 12px; }
.hd-info-product{overflow: hidden;}
.hd-info-pic{ float: left;}
.hd-info-pic-max{width: 500px; height: 500px; border:1px solid #eee; }
.hd-info-pic-min li{ float: left;margin-right: 5px; }
.hd-info-pic-min img{ width: 50px; height: 50px; border:1px solid #eee; }
.hd-info-pic-min img.active{border-color: #129be7;}
.hd-info-text{ margin-left: 550px; }
.hd-info-text-data{ border-bottom: 1px solid #ddd; padding-bottom: 10px; margin-bottom: 20px; }
.hd-info-text-title{ font-size: 2em;}
.hd-info-text-label{ color: #666;margin: 15px 0; }
.hd-info-text-price{font-size: 1.5em;color: #da2525; }
.hd-info-text-attribute{font-size: 14px;}
.hd-info-text-item{margin-bottom: 20px; overflow: hidden;}
.hd-info-text-item dd{ display: inline-block; height: 30px; line-height: 30px; text-align: center; border:1px solid #ddd; margin:5px 10px 0 0; padding: 0 10px; cursor: pointer; }
.hd-info-text-item dd.active{border-color: #129be7;}
.hd-info-text-total{font-size: 14px;}
.hd-info-total-detail{padding: 5px 0; color: #666; text-decoration: underline;}
.hd-info-total-price{margin: 10px 0;}
.hd-info-total-price b{font-size: 2em;color: #da2525; font-weight: normal;}
.hd-info-buy{ color: #999;margin-top: 30px; }
.hd-info-buy-btn{ height: 40px; padding: 0 40px; background-color: #e2393c; color: #fff; cursor: pointer; border:none; border-radius: 5px;margin-right: 20px; }
.hd-info-buy-btn:hover{ background-color: #cd2528; }
.hd-info-tab{margin-top: 30px;}
.hd-info-tab-link{font-size: 14px; border-bottom: 1px solid #ddd; height: 40px;line-height: 40px;}
.hd-info-tab-link a{display: inline-block; width: 150px; text-align: center; color: #000;}
.hd-info-tab-link a.active{ color: #129be7; }
</style>


