<template>
  <div class="hd-model">
	<div class="hd-model-backdrop"></div>
	<div class="hd-model-dialog" >
		<div class="hd-model-content">
			<div class="hd-model-header">
				<h3 class="hd-model-title">{{dialog.title}}</h3>
				<a href="javascript:;" @click="dialog.hideFn">X</a>
			</div>
			<div class="hd-model-body">
				{{dialog.text}}
			</div>
			<div class="hd-model-footer">
				<button type="button" class="hd-model-btn-default" @click="dialog.hideFn" v-if="dialog.btn.cancel!=''">{{dialog.btn.cancel}}</button>
				<button type="button" class="hd-model-btn-ok" @click="dialog.activeFn" >{{dialog.btn.ok}}</button>
			</div>
		</div>
	</div>
  </div>	
</template>
<script>
export default {
  props: ['dialog'],
  methods: {
  }
}
</script>
<style>
.hd-model{position: fixed;top: 0;right: 0;bottom: 0;left: 0;}
.hd-model-backdrop{ position: absolute; left: 0; top: 0; width: 100%; height: 100%; background-color:rgba(0,0,0,0.5);}
.hd-model-dialog{width: 500px; margin: auto;  box-shadow: 0 5px 15px rgba(0,0,0,.5);background-color: #fff;border: 1px solid rgba(0,0,0,.2);border-radius: 6px;outline: 0; position: relative; margin-top: 100px; padding: 10px; }
.hd-model-header{ height: 40px; line-height: 40px; position: relative; border-bottom: 1px solid #eee; }
.hd-model-title{font-weight: normal; font-size: 16px;}
.hd-model-header a{ position: absolute; right: 10px; top: 0; color: #999; }
.hd-model-header a:hover{ color: #268bc4; }
.hd-model-body{min-height: 50px; padding: 10px 0;border-bottom: 1px solid #eee;font-size: 14px;}
.hd-model-footer{margin: 10px 0; text-align: right;}
.hd-model-footer button{display: inline-block;padding: 6px 12px;margin-bottom: 0;font-size: 14px; cursor: pointer; border: 1px solid #ccc; border-radius: 5px;margin-left: 10px;}
.hd-model-btn-default{color: #333;background-color: #fff;border-color: #ccc;}
.hd-model-btn-default:hover{ color: #333;background-color: #e6e6e6;border-color: #adadad;}
.hd-model-btn-ok{color: #fff;background-color: #428bca;border-color: #357ebd;}
.hd-model-btn-ok:hover{ color: #fff;background-color: #3071a9;border-color: #285e8e;}
</style>


