<template>
  <div class="hd-payment">
    <div class="hd-payment-manage-title">Payment method</div>
    <div class="hd-payment-manage-box">
      <h3>My payment card <span>( Added <b>{{payment.length}}</b> credit card )</span></h3>
      <div class="hd-payment-manage-content">
        <div class="hd-payment-manage-item" v-for="item in payment">
        <div class="hd-payment-manage-item-info">
          <p><span>Name on card:</span>{{item.name}}</p>
          <p><span>Card number:</span><b v-text="item.num | splitString"></b></p>
          <p><span>Expiration data:</span>{{item.day}}</p>
          <p class="hd-payment-manage-item-action"><a href="">Edit</a><a href="javascript:;" @click="showDialog($index)">Delete</a></p>
         </div> 
        </div>
      </div>
      <div class="hd-payment-manage-add">
        <a v-link="{path:'/account/payment/add'}">
          <i class="iconfont">&#xe654;</i>
          <p>Add payment cards</p>
        </a>
      </div>
    </div>
  </div>
<dialog v-show="dialog.show"  :dialog='dialog' transition="fade" ></dialog>
</template>

<script>
import dialog from '../common/model'
export default {
  data () {
    let self = this
    return {
      dialog: {
        show: false,
        text: 'Are you sure you want to delete this payment method?',
        title: 'Delete Payment Method',
        btn: { ok: 'Ok', cancel: 'Cancle' },
        hideFn: function () { self.dialog.show = false },
        activeFn: function () {
          self.dialog.show = false
          self.payment.splice(self.delIndex, 1)
        }
      },
      delIndex: '',
      payment: [ { name: 'Sivan', num: '654548736542147', day: '03/21' }, { name: 'Allen', num: '6221458732145698', day: '08/19' } ]
    }
  },
  methods: {
    showDialog (index) {
      this.dialog.show = true
      this.delIndex = index
    }
  },
  components: {
    dialog
  },
  events: {
  }
}
</script>

<style>
.hd-payment-manage-title{height: 30px; line-height: 30px; border-bottom:1px solid #ddd; font-weight: 600; font-size: 1.2em;margin-bottom: 20px; }
.hd-payment-manage-box h3{margin-bottom: 20px;font-size: 1em;}
.hd-payment-manage-box h3 span{ margin-left: 50px; font-weight: 400; color: #666; }
.hd-payment-manage-box h3 span b{ color: #da2525; margin: 0 5px; }
.hd-payment-manage-item{ border:1px solid #ddd; float: left;width: 30%; margin-right: 3%; margin-bottom: 10px; }
.hd-payment-manage-item:hover{background-color: #fafafa;}
.hd-payment-manage-item-info{ padding: 10px; } 
.hd-payment-manage-item-info p{ line-height: 35px; }
.hd-payment-manage-item-info p span{ display: inline-block; width:100px; color: #999;  }
.hd-payment-manage-item-action a{ margin-right: 20px; color: #0b72ac; }
.hd-payment-manage-item-action a:hover{ color: #129be7; text-decoration: underline; }
.hd-payment-manage-add{ text-align: center; border:1px solid #ddd; float: left;width: 30%; margin-right: 3%; height: 160px;}
.hd-payment-manage-add a{ color: #129be7; }
.hd-payment-manage-add a:hover{ color: #0b72ac;}
.hd-payment-manage-add a i{font-size: 3em; display: block; margin-top: 30px;}
</style>


