module.exports = "<div class=hd-address> <div class=hd-address-select> <div class=hd-address-select-title> <h3>Select a shipping address</h3> <p>Is the address you'd like to use displayed below? If so, click the corresponding \"Ship to this address\" button. Or you can <a href=\"\">enter a new shipping address</a> </p> </div> <div class=hd-address-empty v-show=addressEmpty> <i class=iconfont>&#xe64e;</i>The shopping address is empty, go and add some.<a href=\"\">Add shipping address</a> </div> <div class=hd-address-box> <div class=hd-address-item v-for=\"item in addressItem\" :class=\"{active:currentAddress==$index}\" @click=\"currentAddress=$index\"> <p class=hd-address-item-name>{{item.name}}</p> <p class=hd-address-item-city>{{item.city}}</p> <p class=hd-address-item-line>{{item.line1}}</p> <p class=hd-address-item-line>{{item.line2}}</p> <p class=hd-address-item-country>{{item.country}}</p> <p class=hd-address-item-phone>{{item.phone}}</p> <p class=hd-address-item-ac><a @click.stop=$event.stopPropagation()>Edit</a><a @click.stop=$event.stopPropagation() @click=showDialog($index)>Delete</a></p> </div> </div> </div> <div class=hd-shoppingList> <div class=hd-shoppingList-title> <h3>Shopping List</h3> <a v-link=\"{path:'/cart'}\">Change quantities or delete</a> </div> <div class=hd-shoppingList-box> <div class=hd-shoppingList-item v-for=\"item in shoppingList\"> <div class=hd-shoppingList-item-pic><img :src=item.src alt=\"\"></div> <div class=hd-shoppingList-item-info> <p class=hd-shoppingList-item-title>{{item.title}}</p> <p class=hd-shoppingList-item-model>{{item.model}}</p> </div> <div class=hd-shoppingList-item-price> <p>Price: <b>{{item.price}}</b></p> </div> <div class=hd-shoppingList-item-num> <p>{{item.num}}</p> </div> <div class=hd-shoppingList-item-fatory> <p>{{item.factory}}</p> </div> </div> </div> </div> <div class=hd-shoppingList-money> <p><span class=title>{{totalDate.all}} items:</span><span class=value>${{totalDate.money}}</span></p> <p><span class=title>preferntial:</span><span class=value>-${{preferntial.toFixed(2)}}</span></p> <p><span class=title>shipment:</span><span class=value>${{shipment.toFixed(2)}}</span></p> <p><span class=title>subtotal:</span><span class=value><b>${{totalDate.money-preferntial+shipment}}</b></span></p> <p><a v-link=\"{path:'/buy2'}\" class=hd-shoppingList-money-pay>Payment</a></p> </div> </div> <dialog v-show=dialog.show :dialog=dialog transition=fade></dialog>";


