<template>
<div class="hd-payment">
	<div class="hd-payment-order">
		<div class="hd-payment-order-info">
			<span class="hd-payment-order-info-tips">{{orderData.text}}</span>
			<span class="hd-payment-order-info-num">Order number:<a href="">{{orderData.num}}</a></span>
			<span class="hd-payment-order-info-total">Total:<b>${{orderData.money}}</b></span>
		</div>
		<div class="hd-payment-order-text">
			Now please choose you'd like to pay below, and we'll save it as an option.
		</div>
	</div>
	<div class="hd-payment-cnt">
		<div class="hd-payment-title">
			<h3>Select a payment method</h3>
			<p>Enter how you'd like to pay below, and we'll save it as an option.</p>
		</div>
		<div class="hd-payment-empty" v-show="paymentEmpty">
	      <i class="iconfont">&#xe64f;</i>The payment method is empty, go and add some.<a href="">Add payment method</a>
	    </div>
		<div class="hd-payment-box">
			<div class="hd-payment-item" v-for="item in payment"  :class="{active:currentPayment==$index}" @click="currentPayment=$index" >
				<p class="hd-payment-item-bank"><img :src="banksrc(item.bank)" alt=""></p>
				<p class="hd-payment-item-name">{{item.name}}</p>
				<p class="hd-payment-item-num" v-text="item.num | splitString"></p>
				<p class="hd-payment-item-day">{{item.expiration}}</p>
				<p class="hd-payment-item-action"><a href="javascript:;"  @click="showDialog($index)">Delete</a></p>
			</div>
		</div>
	</div>
	<div class="hd-payment-add">
	   <div class="hd-payment-add-title">
		   	<h3 class="">Add a Payment method</h3>
			<h4>Credit or Debit Cards</h4>
			<p>HDHaw accepts all major credit and dedit cards:</p>
			<p class="hd-payment-add-h"><i class="iconfont">&#xe650;</i>Add a Card</p>
			<p>Enter your card infomation:</p>
	   </div>
		
		<form class="hd-form-addcard">
            <div class="hd-form-inline">
              <label class="" for="name">Name on card</label>
              <input type="text" class="form-control" id="name" placeholder="Name" v-model="cardName">
            </div>
			<div class="hd-form-inline">
              <label class="" for="num">Card number</label>
              <input type="text" class="form-control" id="num" placeholder="Card number" v-model="cardNumber">
            </div>
            <div class="hd-form-inline">
              <label class="" for="">Expiration date</label>
              <select v-model="cardMonth">
              	<option v-for="item in month" :value="item.value">{{item.text}}</option>
              </select>
              <select v-model="cardYear">
              	<option v-for="item in year" :value="item.value">{{item.text}}</option>
              </select>
            </div>
            <div class="hd-form-inline">
				<button type="button" class="hd-form-addcard-btn" @click="addCard">Add your card</button>
            </div>
        </form>
	</div>
	<div class="hd-payment-submit">
		<a href="javascript:;" @click="dialog2.show=true">Payment</a>
	</div>
</div>
<dialog v-show="dialog.show"  :dialog='dialog' transition="fade"></dialog>
<dialog v-show="dialog2.show"  :dialog='dialog2' transition="fade"></dialog>
</template>
<script>
import dialog from '../common/model'

export default {
  props: ['data'],
  data () {
    let self = this
    return {
      orderData: { text: 'Please make timely payments to the order as soon as possible!', num: '12703955169', money: '650.00' },
      payment: [ { bank: 'visa', name: 'Sivan', num: '6221548736542147', expiration: '03/21' }, { bank: 'unionPay', name: 'Allen', num: '1654548736542147', expiration: '03/19' } ],
      currentPayment: 0,
      month: [ { text: '01', value: '01' }, { text: '02', value: '02' }, { text: '03', value: '03' }, { text: '04', value: '04' } ],
      year: [ { text: '2016', value: '2016' }, { text: '2017', value: '2017' }, { text: '2018', value: '2018' }, { text: '2019', value: '2019' } ],
      cardMonth: '01',
      cardYear: '2016',
      delIndex: '',
      paymentEmpty: false,
      dialog: {
        show: false,
        text: 'Are you sure you want to delete this payment method?',
        title: 'Delete Payment Method',
        btn: { ok: 'Ok', cancel: 'Cancle' },
        hideFn: function () { self.dialog.show = false },
        activeFn: function () {
          self.dialog.show = false
          self.payment.splice(self.delIndex, 1)
          if (self.payment.length <= 0) {
            self.paymentEmpty = true
          }
        }
      },
      dialog2: {
        show: false,
        text: 'Please open a new online banking payment page, before payment completion please do not close the window.',
        title: 'Prompt Payment',
        btn: { ok: 'Ok', cancel: 'Cancle' },
        hideFn: function () { self.dialog2.show = false },
        activeFn: function () {
          self.dialog2.show = false
          self.$route.router.go({path: '/account/order'})
        }
      }
    }
  },
  methods: {
    banksrc (bank) {
      var name = bank.toLowerCase()
      var src
      if (name.indexOf('unionpay') > -1) {
        src = require('../../assets/images/china.png')
      } else {
        src = require('../../assets/images/visa.png')
      }
      return src
    },
    showDialog (index) {
      this.dialog.show = true
      this.delIndex = index
    },
    addCard () {
      var paymentMethod = {}
      paymentMethod.bank = 'visa'
      paymentMethod.name = this.cardName
      paymentMethod.num = this.cardNumber
      paymentMethod.expiration = this.cardMonth + '/' + this.cardYear.substring(2)
      this.payment.unshift(paymentMethod)
    }
  },
  computed: {
  },
  components: {
    dialog
  },
  events: {
    hideDialog () {
      this.dialogState = false
    },
    dialogFun () {
      this.dialogState = false
      this.payment.splice(this.delIndex, 1)
      if (this.payment.length <= 0) {
        this.paymentEmpty = true
      }
    },
    hidePayment () {
      this.paymentState = false
    },
    goOrder () {
      this.$route.router.go({path: '/account/order'})
    }
  }
}
</script>
<style>
.hd-payment{max-width: 1200px; min-width: 1000px; margin: auto;font-size: 12px;}
.hd-payment-order{background-color: #ebfbe1; border:1px solid #d7efca; border-radius: 5px; padding:15px 10px;}
.hd-payment-order-info-tips{ font-size: 14px; font-weight: 600; }
.hd-payment-order-info-num{ margin: 0 30px; }
.hd-payment-order-info-num a{ color: #129be7; margin-left: 5px;}
.hd-payment-order-info-num a:hover{ color: #0a72ac; text-decoration: underline;}
.hd-payment-order-info-total b{color: #da2525; margin-left: 5px; font-size: 14px; font-weight: 600; }
.hd-payment-order-text{ color: #999;line-height: 20px; }
.hd-payment-cnt{margin: 20px 0;}
.hd-payment-title{border-bottom: 1px solid #eee;padding-bottom: 10px;margin-bottom: 20px;}
.hd-payment-title h3{font-size: 18px;line-height: 40px;}
.hd-payment-title p{ color: #999; }
.hd-payment-box{overflow: hidden;}
.hd-payment-item{ border:1px solid #ddd; float: left;width: 25%; margin-right: 5%; cursor: pointer; margin-bottom: 10px; }
.hd-payment-item.active{ border-color: #ed0808; background: url('../../assets/images/select.png') right bottom no-repeat; }
.hd-payment-item p{ padding:5px 10px;}
.hd-payment-item-bank{background-color: #f9f9f9; height: 30px; line-height: 30px; text-align: right;}
..hd-payment-item-bank img{ width: 40px; height: 28px; }
.hd-payment-item-name{font-size: 14px; font-weight: 600; }
.hd-payment-item-action{margin: 5px 0}
.hd-payment-item-action a{ color: #129be7; margin-right: 20px; }
.hd-payment-item-action a:hover{ text-decoration: underline;color: #0a72ac;}
.hd-payment-empty{ height: 60px; line-height: 60px; color: #333; text-align: center; border:1px solid #ddd; margin: 20px 0; background-color: #f9f9f9; }
.hd-payment-empty a{display: inline-block; height: 25px;line-height: 25px; margin-left: 10px; padding: 0 20px; color: #fff; background-color: #e33b3e; border-radius: 5px;}
.hd-payment-empty a:hover{background-color: #cd2528;}
.hd-payment-add{ background: url('../../assets/images/banks.jpg') 600px 50px no-repeat; }
.hd-payment-add-title h3{ font-size: 20px; padding-bottom: 10px;border-bottom: 1px solid #eee; }
.hd-payment-add-title h4{font-size: 16px; line-height: 40px; color: #555;}
.hd-payment-add-title p{line-height: 25px;}
.hd-payment-add-h{ color: #129be7; }
.hd-payment-add-h i{ color: #666; }
.hd-form-addcard{ overflow: hidden;margin-top: 5px; }
.hd-form-inline{ display: inline-block;margin-bottom: 5px;vertical-align: middle;margin-right: 10px;}
.hd-form-inline input,.hd-form-inline select{ padding: 5px; border-radius: 2px; border:1px solid #ccc;color: #129be7; }
.hd-form-inline label{ font-weight: 600; display: block; }
.hd-form-addcard-btn{ padding: 8px 20px; border-radius: 4px; border: none; margin-top: 17px; background-color: #0a72ac; color: #fff; cursor: pointer;}
.hd-form-addcard-btn:hover{background-color: #129be7;}
.hd-payment-submit{margin-top: 30px; border-top: 1px solid #eee; overflow: hidden;}
.hd-payment-submit a{display: block; width: 180px; height: 35px; line-height: 35px; text-align: center; color: #fff; border-radius: 5px; background-color: #e2383b; float: right; margin-top: 30px;}
.hd-payment-submit a:hover{background-color: #c33033;}
</style>


