<template>
<div class="hd-guide">
	<div class="hd-guide-top"> 
        <div class="hd-top-content">
              <div class="hd-top-link">
                <a href="" target="_black"><i class="iconfont">&#xe606;</i>Login Ecasazone</a>
                <a href="javascript:;" @mouseenter='showApp' @mouseout='hideApp' ><i class="iconfont" >&#xe60b;</i>Tenvis APP</a>
                <div class="hd-top-app" v-show='showCode'><img src="../../assets/images/code.png" alt="app"></div>
              </div>
              <div class="hd-top-login">
                <a v-link="{path:'/login'}"><i class="iconfont">&#xe602;</i>Sign in or Creat an account</a>
                <a v-link="{path:'/account/order'}"><i class="iconfont">&#xe603;</i>My Order</a>
              </div>
        </div>
    </div>
    <div class="hd-guide-step">
    	<div class="hd-guide-logo">
    		 <a v-link="{path:'/index'}"><img src="../../assets/images/logo.png" alt="logo"></a>
    	</div>
    	<div class="hd-guide-step-box">
    		<div class="hd-guide-step-bg"></div>
    		<div class="hd-guide-step-ac" v-bind:style="{ width: currentW }"></div>
    		<div class="hd-step-box">
	    		<div class="hd-step-guide-item active" >
	    			<b>1</b>
	    			<p>Shopping Cart</p>
	    		</div>
	    		<div class="hd-step-guide-item" :class="{active:current==2||current==3}">
	    			<b>2</b>
	    			<p>Check Order</p>
	    		</div>
	    		<div class="hd-step-guide-item" :class="{active:current==3}">
	    			<b>3</b>
	    			<p>Submit Order</p>
	    		</div>
    		</div>
    	</div>
    	
    </div>
</div>
</template>
<script>
export default {
  props: ['data'],
  data () {
    return {
      showCode: false,
      current: this.data
    }
  },
  methods: {
    showApp () {
      this.showCode = true
    },
    hideApp () {
      this.showCode = false
    }
  },
  computed: {
    currentW () {
      let total = this.data / 3 * 100 + '%'
      return total
    }
  }
}
</script>
<style>
.hd-guide{}
.hd-guide-top{height: 35px; width: 100%;background-color: #f1f1f1; color: #666;font-size: 12px;line-height: 35px; }
.hd-top-content{max-width: 1200px;min-width: 1000px;margin: auto;}
.hd-top-link{float: left; position: relative; height: 35px;}
.hd-top-link a{ color: #666;margin-right: 20px; }
.hd-top a:hover{color: #0a72ac; }
.hd-top-app{position:absolute; height: 100px; width: 100px; left: 142px;top: 35px;border:5px solid #eee; }
.hd-top-login{float: right; height: 35px;}
.hd-top-login a{margin-left: 20px; cursor:pointer; color: #666;}
.hd-guide-step{width: 1200px; height: 140px; margin: auto; position: relative;border-bottom: 1px solid #eee;margin-bottom: 20px;}
.hd-guide-logo{ float: left;margin-top: 30px; height: 80px; }
.hd-guide-logo a{display: block;}
.hd-guide-step-box{ float: right; width: 500px; height: 50px;margin-top: 50px; position: relative;}
.hd-guide-step-bg,.hd-guide-step-ac{ height: 5px; width:100%;  border-radius: 5px;  position: absolute; top: 20px; left: 0;}
.hd-guide-step-bg{ background-color: #ccc;}
.hd-guide-step-ac{ background-color: #7abd54; }
.hd-step-guide-item{ width: 33.3%; float: left; text-align: center; font-size: 12px; position: relative;padding-top: 10px; }
.hd-step-guide-item b{ display: inline-block; width: 20px; height: 20px;line-height: 20px; text-align: center; border-radius: 50%; background-color: #ccc; color: #fff; }
.hd-step-guide-item p{ line-height: 40px; color: #ccc; }
.hd-step-guide-item.active b{ background-color: #7abd54; }
.hd-step-guide-item.active p{ color:#7abd54;  }
</style>


