<template>
  <div class="hd-info-tech">
    <div class="hd-info-tech-item" v-for="item in tech">
      <div class="hd-info-tech-category">
        <p><i class="iconfont">{{{item.icon}}}</i>{{item.title}}</p>
      </div>
      <div class="hd-info-tech-data">
        <p  v-for="i in item.data"><span>{{i.key}}</span>{{i.value}}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      tech: [
      { title: 'Image Sensor', icon: '&#xe641;', data: [{ key: 'Sensor:', value: 'High Definition Color CMOS Sensor' }, { key: 'Display Resolution:', value: '1280 x 720' }, { key: 'Min. Illumination:', value: '0.1 Lux' }] },
      { title: 'Lens', icon: '&#xe640;', data: [{ key: 'Lens Type:', value: '6 Glass Lens' }, { key: 'Focal Length:', value: 'f:3.6mm' }, { key: 'Aperture:', value: 'F1.8' }, { key: 'Angle of View:', value: '71' }] },
      { title: 'Video', icon: '&#xe649;', data: [{ key: 'Image Compression:', value: 'H.264' }, { key: 'Image Frame Rate:', value: '25fps maxmium' }, { key: 'Resolution:', value: '720P(1280 x 720), VGA(640 x 360), QVGA(320 x 180)' }, { key: 'Stream:', value: 'Dual Stream' }, { key: 'Flip Image:', value: 'Flip and Mirror' }, { key: 'Pan/Tilt Angle:', value: 'Horizontal:340 & Vertical: 90' }, { key: 'Infrared LED:', value: '30pcs IR-LEDs' }, { key: 'Night Vision Distance:', value: '8 M' }] },
      { title: 'Network', icon: '&#xe646;', data: [{ key: 'Input/Output:', value: 'Two-way Audio, Built-in Mic & Speaker' }, { key: 'Audio Compression:', value: 'G.711/G.726' }] },
      { title: 'Audio', icon: '&#xe643;', data: [{ key: 'Ethernet:', value: '10/100Mbps' }, { key: 'Wireless Standard:', value: 'IEEE802.11b/g/n' }, { key: 'Wireless Security:', value: 'WEP, WPA, WPA2' }, { key: 'Network Protocol:', value: 'IPTCPUDPHTTPSMTPFTPDHCPDDNSRTSPICMPNTPUPNPRTPP2PONVIFWPS' }] },
      { title: 'System Requirements', icon: '&#xe644;', data: [{ key: 'Operating System:', value: 'Microsoft Windows XP, WIN7, Mac OS, iOS, Android' }, { key: 'Browser:', value: 'Microsoft IE7 and above version;Mozilla Firefox; Google Chrome;Apple Safari' }] },
      { title: 'Main Features', icon: '&#xe648;', data: [{ key: 'Motion Detection:', value: 'Alarm via E-Mail, upload alarm snapshot to FTP' }, { key: 'User Accounts:', value: 'Two levels user role' }, { key: 'Reset:', value: 'Reset button is available' }] },
      { title: 'Power', icon: '&#xe647;', data: [{ key: 'Power Supply:', value: 'DC 5V/2.0A' }, { key: 'Power Consumption:', value: '6.9 Watts (Max.)' }] },
      { title: 'Environment', icon: '&#xe642;', data: [{ key: 'OperatingTemperature:', value: '-20 ~ 55C (-4F ~ 131F)' }, { key: 'Operating Humidity:', value: '10% ~ 85% non-condensing' }, { key: 'Storage Temperature:', value: '-20C ~ 60 (-4F ~ 140F)' }, { key: 'Storage Humidity:', value: '0% ~ 90% non-condensing' }] },
      { title: 'Others', icon: '&#xe645;', data: [{ key: 'Package Dimension:', value: '199.5(L)*169.5(W)*112(H) mm' }, { key: 'Net Weight:', value: '' }, { key: 'Gross Weight:', value: '' }, { key: 'Certification:', value: 'CE, FCC, RoHS' }, { key: 'Warranty:', value: 'Limited 1-year warranty' }] }
      ]

    }
  },
  methods: {
    changeImg (e, num) {
      this.imgsrc = e.target.src
      this.activeImg = num
    },
    tabShow (num) {
      this.current = num
    }
  }
}
</script>

<style>
.hd-info-tech-item{ overflow: hidden;border-bottom: 1px solid #eee;font-size: 12px;padding: 20px 0; }
.hd-info-tech-category{ width: 20%; float: left; height: 100%; vertical-align: middle; }
.hd-info-tech-category p{color: #666;font-weight: normal;width: 100px;text-align: center;}
.hd-info-tech-category p i{display: block;font-size: 40px;}
.hd-info-tech-data{width: 80%; float: left;}
.hd-info-tech-data p{line-height: 30px;word-wrap: break-word;word-break: break-all;}
.hd-info-tech-data p span{display: inline-block;width: 150px;font-weight: bolder;margin-right: 30px;line-height: 20px;padding-bottom: 10px;}
</style>


