module.exports = "<div class=hd-cart-warpper> <div class=hd-login-prompt> <i class=iconfont>&#xe64a;</i>You are not logged in! After logging in the cart so items will be saved to your account in <a v-link=\"{path:'/login'}\">Log In</a> </div> <div class=hd-cart-box> <div class=hd-cart-title> <h3><i class=iconfont>&#xe600;</i> Shopping Cart</h3> </div> <div class=hd-cart-empty v-show=cartEmpty> <i class=iconfont>&#xe615;</i>The shopping cart is empty, go and buy some.<a v-link=\"{path:'/product'}\">Go Shopping</a> </div> <div class=hd-cart-cnt> <div class=hd-cart-item v-for=\"item in cartDate\" transition=staggered stagger=100> <div class=hd-cart-item-pic><img :src=item.src alt=\"\"></div> <div class=hd-cart-item-info> <p>{{item.title}}</p> <p class=hd-cart-item-info-model>{{item.model}}</p> </div> <div class=hd-cart-item-price> Price: <b>{{item.price}}</b> </div> <div class=hd-cart-item-num> <p><a href=javascript: @click=minus(item) class=hd-cart-item-num-minus>-</a><span>{{item.num}}</span><a href=javascript:; @click=plus(item) class=hd-cart-item-num-plus>+</a></p> </div> <div class=hd-cart-item-total> Total: <b>${{ (item.price.substring(1) * item.num).toFixed(2) }}</b> </div> <div class=hd-cart-item-stock> <p :class=\"{active:item.state=='in stock'}\">{{item.state}}</p> </div> <div class=hd-cart-item-action> <a href=javascript:; @click=showDialog($index)><i class=iconfont>&#xe64b;</i>Delete</a> </div> </div> </div> <div class=hd-cart-money v-show=!cartEmpty> <p class=hd-cart-money-total>Subtotal ( {{allItem}} items ): <b>${{totalPrice}}</b></p> <p class=hd-cart-money-check><button type=button v-link=\"{path:'/buy1'}\">Check Out</button></p> </div> </div> </div> <dialog v-show=dialog.show :dialog=dialog transition=fade></dialog>";


