module.exports = function (router) {
  router.map({
    '/index': {
      component: require('./components/index/index.vue')
    },
    '/load': {
      component: require('./components/common/loading.vue')
    },
    '/search': {
      component: require('./components/index/search.vue')
    },
    '/search/:brand': {
      component: require('./components/index/search.vue')
    },
    '/product': {
      component: require('./components/product/product.vue')
    },
    '/list': {
      component: require('./components/product/productList.vue'),
      subRoutes: {
        '/ipcam': {
          component: require('./components/product/ipcam.vue')
        },
        '/nvr': {
          component: require('./components/product/nvr.vue')
        },
        '/tester': {
          component: require('./components/product/tester.vue')
        },
        '/other': {
          component: require('./components/product/other.vue')
        }
      }
    },
    '/other': {
      component: require('./components/product/productList.vue')
    },
    '/login': {
      component: require('./components/login/login.vue')
    },
    '/register': {
      component: require('./components/login/register.vue')
    },
    '/register/success': {
      component: require('./components/login/registerSuccess.vue')
    },
    '/forgot': {
      component: require('./components/login/forgot.vue')
    },
    '/forgot/check': {
      component: require('./components/login/checkEmail.vue')
    },
    '/reset': {
      component: require('./components/login/reset.vue')
    },
    '/reset/success': {
      component: require('./components/login/resetSuccess.vue')
    },
    '/about': {
      component: require('./components/about/about.vue'),
      subRoutes: {
        '/home': {
          component: require('./components/about/aboutUs.vue')
        },
        '/agent': {
          component: require('./components/about/agent.vue')
        },
        '/contact': {
          component: require('./components/about/contact.vue')
        }
      }
    },
    '/info': {
      component: require('./components/product/productInfo.vue'),
      subRoutes: {
        '/tech': {
          component: require('./components/product/tech.vue')
        },
        '/design': {
          component: require('./components/product/design.vue')
        },
        '/faq': {
          component: require('./components/product/faq.vue')
        }
      }
    },
    '/cart': {
      component: require('./components/shipping/cart.vue')
    },
    '/addtocart': {
      component: require('./components/shipping/addToCart.vue')
    },
    '/buy1': {
      component: require('./components/shipping/selectAddress.vue')
    },
    '/buy2': {
      component: require('./components/shipping/selectPayment.vue')
    },
    '/support': {
      component: require('./components/support/support.vue'),
      subRoutes: {
        '/list': {
          component: require('./components/support/supportList.vue')
        },
        '/info': {
          component: require('./components/support/supportInfo.vue')
        }
      }
    },
    '/account': {
      component: require('./components/account/account.vue'),
      subRoutes: {
        '/home': {
          component: require('./components/account/accountHome.vue')
        },
        '/order': {
          component: require('./components/account/accountOrder.vue')
        },
        '/payment': {
          component: require('./components/account/accountPayment.vue')
        },
        '/address': {
          component: require('./components/account/accountAddress.vue')
        },
        '/personal': {
          component: require('./components/account/accountPersonal.vue')
        },
        '/order/info': {
          component: require('./components/account/accountOrderInfo.vue')
        },
        '/payment/add': {
          component: require('./components/account/accountPaymentAdd.vue')
        },
        '/address/edit': {
          component: require('./components/account/accountAddressEdit.vue')
        }
      }
    },
    '/error': {
      component: require('./components/common/404.vue')
    },
    '*': {
      component: require('./components/index/index.vue')
    }
  })
  router.beforeEach(function () {
    // window.scrollTo(0, 0)
  })

  router.redirect({
    '*': '/index'
  })

  router.alias({
    '/list': '/list/ipcam',
    '/account': '/account/home',
    '/info': '/info/design',
    '/support': '/support/list',
    '/about': '/about/home'
  })

  // router.beforeEach((transition) => {
  //   if (transition.to.path === '/account/order') {
  //     transition.abort()
  //   } else {
  //     transition.next()
  //   }
  // })
}



