<template>
<div class="hd-agent">
  <div class="hd-agent-title">Becoming A Reseller Or Distributor</div>
  <div class="hd-agent-tips">Become an authorised HDHawk reseller and increase your revenue streams. Authorised resellers are supported through HDHawk marketing activities, sales support and priority communication lines to our technicalteam and customer service department.</div>
  <div class="hd-agent-support">
    <ul>
      <li>Trade pricing</li>
      <li>Listing as HDHawk authorised reseller / installer on HDHawk website</li>
      <li>Inclusion in HDHawk marketing activities and promotions</li>
      <li>Authorization to use HDHawk logo and other HDHawk copyright material</li>
      <li>Access to HDHawk marketing material and technical information</li>
      <li>Priority technical, sales and customer service support</li>
    </ul>
  </div>
  <div class="hd-agent-form-box">
    <div class="hd-agent-form-tips">To apply, email our Sales Manager via <b>admin#tenvis.com</b>(# change @) or send us your details below:</div>
    <form  class="hd-agent-form" novalidate>
      <div class="hd-agent-form-item">
        <label for="name"><em>*</em>Your Name</label><input type="text" name="" id="name" v-model='name'>
      </div>
      <div class="hd-agent-form-item">
        <label for="email"><em>*</em>Email</label><input type="text" name="" id="email" v-model='email'>
      </div>
      <div class="hd-agent-form-item">
        <label for="telephone"><em></em>Telephone</label><input type="text" name="" id="telephone" v-model='phone'>
      </div>
      <div class="hd-agent-form-item">
        <label for="website"><em></em>Website</label><input type="text" name="" id="website" v-model='website'>
      </div>
      <div class="hd-agent-form-item">
        <label for="country"><em></em>country</label><input type="text" name="" id="country" v-model='country'>
      </div>
      <div class="hd-agent-form-item">
        <label for="message"><em>*</em>Message</label><textarea name="" id="" cols="30" rows="10" v-model='msg'></textarea>
      </div>
      <div class="hd-agent-form-item">
        <label for="message"></label><button type="button" class="hd-agent-form-item-btn" @click="this.dialog.show = true">Submit</button>
      </div>
      <div class="hd-agent-form-submit-tips">We will contact you within 24 hours after your information is submitted, thank you!</div>
    </form>
  </div>
</div>
<dialog v-show="dialog.show"  :dialog='dialog' transition="fade"></dialog>
</template>

<script>
import dialog from '../common/model'
export default {
  data () {
    let self = this
    return {
      dialog: {
        show: false,
        text: 'We have received your application we have received, our professional staff will be in 24 hours to get in touch with you, thank you!',
        title: 'Note',
        btn: { ok: 'Ok', cancel: '' },
        hideFn: function () { self.dialog.show = false },
        activeFn: function () { self.dialog.show = false }
      }
    }
  },
  methods: {
  },
  events: {
  },
  components: {
    dialog
  }
}
</script>

<style>
.hd-agent-title{ height: 30px; font-weight: 600; font-size: 1.2em; border-bottom: 1px solid #ddd; }
.hd-agent-tips{margin: 20px 0; color: #999;}
.hd-agent-support{ background-color: #f7fdff; border: 1px solid #bde3f1; padding: 20px;margin-bottom: 20px; }
.hd-agent-support li{ color: #666; list-style: inside circle; line-height: 30px;}
.hd-agent-form-box{ background-color: #fafafa; border: 1px solid #eee;padding:15px}
.hd-agent-form-tips{ margin-bottom: 10px; color: #666; }
.hd-agent-form-tips b{ color: #ff8080;margin: 0 5px; }
.hd-agent-form-item{margin: 30px 0;}
.hd-agent-form-item label{ display: inline-block; width: 80px; text-align: right; margin-right: 20px; vertical-align: top; }
.hd-agent-form-item label em{ color: #e80f0f; display: inline-block;width: 5px; }
.hd-agent-form-item input[type='text']{ width: 300px; border: none;border-bottom: 1px solid #ddd; padding: 5px 10px;background-color: #fafafa; color: #129be7; }
.hd-agent-form-item textarea{ vertical-align: top; width: 500px; height: 200px; border: 1px solid #ddd; padding: 5px;color: #129be7;  }
.hd-agent-form-item-btn{ padding: 8px 40px; color: #fff; background-color: #129be7; border-radius: 4px;border: none; cursor: pointer; }
.hd-agent-form-item-btn:hover{background-color: #0b72ac;}
.hd-agent-form-submit-tips{padding-left: 100px;margin-bottom: 20px; color: #999;}
</style>


