<template>
  <div id='warpper'>
    <app-header  :keyword="searchText || $route.params.brand"></app-header>
    <app-menu></app-menu>
    <div class="hd-search-result">
      <div class="hd-search-result-title">About <b>"{{searchText}}"</b>, there are {{searchNum}} search results:</div>
      <div class="hd-search-left">
        <h3>Related Products</h3>
        <div class="hd-search-relate">
          <div class="hd-search-item" v-for="item in relate">
          <a v-link="{path:'/info/design'}">
            <p class="hd-search-item-pic"><img :src="item.src" alt=""></p>
            <p class="hd-search-item-price"><b>{{item.price}}</b></p>
            <p class="hd-search-item-text">{{item.text}}</p>
          </a>  
          </div>
        </div>
      </div>
      <div class="hd-search-right">
        <div class="hd-cart-empty" v-show="searchEmpty">
          <i class="iconfont">&#xe618;</i>About <b>"{{searchText}}"</b>, there is 0 search results,please try other keywords !
        </div>
        <ul class="fn-clear">
          <li class="hd-product-info" v-for="item in result | filterBy searchText in 'text'" transition="staggered" stagger="100"><a v-link="{path:'/info'}">
            <img :src="item.src" alt="">
            <p class="hd-product-info-text">{{item.text}}</p>
            <p class="hd-product-info-price"><b>{{item.price}}</b></p>
          </a></li>
      </ul>
      </div>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
import appHeader from '../common/appHeader'
import appMenu from '../common/appMenu'
import appFooter from '../common/appFooter'

export default {
  data () {
    return {
      result: [
      {src: require('../../assets/images/f1.jpg'), text: 'HIKVISION 3MP IR PoE HD Bullet Network/Wireless WIFI Surveillance Camera', price: '$79.99'},
      {src: require('../../assets/images/f2.jpg'), text: 'HIKVISION DS-2CD2T45-I8 4MP H.265 PoE Outdoor Bullet Network Surveillance Camera', price: '$85.00'},
      {src: require('../../assets/images/f5.jpg'), text: 'IKVISION DS-2CD2145F-IS 4MP PoE Vandal-proof Dome Network Surveillance Camera', price: '$125.99'},
      {src: require('../../assets/images/item2.png'), text: 'TENVIS TZ100 HD Wireless IP/Network Security Camera', price: '$63.99'},
      {src: require('../../assets/images/new5.jpg'), text: 'TENVIS IP391W-HD Outdoor Wireless Waterproof Bullet IP/Network Security Surveillance Camera', price: '$65.99'},
      {src: require('../../assets/images/new4.jpg'), text: 'TENVIS IP602W Outdoor Wireless Waterproof Bullet IP/Network Security Surveillance Camera', price: '$79.00'},
      {src: require('../../assets/images/hot4.jpg'), text: 'TH661- Wi-Fi HD P2P Pan/Tilt Smart IP Camera ', price: '$69.00'},
      {src: require('../../assets/images/new1.jpg'), text: 'JPT3815W - Pan/Tilt Wireless IP Camera Night Vision - Black', price: '$64.99'},
      {src: require('../../assets/images/new2.jpg'), text: 'IPROBOT3 - HD P2P Megapixel IP Camera - Silver', price: '$63.00'},
      {src: require('../../assets/images/new3.jpg'), text: 'TH692- Outdoor P2P HD Waterproof IP Camera', price: '$110.00'}
      ],
      relate: [
      {src: require('../../assets/images/f1.jpg'), text: 'HIKVISION 3MP IR PoE HD Bullet Network/Wireless WIFI Surveillance Camera', price: '$79.99'},
      {src: require('../../assets/images/f2.jpg'), text: 'HIKVISION DS-2CD2T45-I8 4MP H.265 PoE Outdoor Bullet Network Surveillance Camera', price: '$85.00'},
      {src: require('../../assets/images/f5.jpg'), text: 'IKVISION DS-2CD2145F-IS 4MP PoE Vandal-proof Dome Network Surveillance Camera', price: '$125.99'}
      ],
      searchEmpty: false,
      searchNum: 0,
      searchText: ''
    }
  },
  components: {
    appHeader,
    appMenu,
    appFooter
  },
  methods: {
  },
  events: {
    search (keyword) {
      this.searchText = keyword
      this.searchNum = this.$options.filters.filterBy(this.result, this.searchText).length
      if (this.searchNum === 0) {
        this.searchEmpty = true
      } else {
        this.searchEmpty = false
      }
    }
  },
  ready () {
    this.searchText = this.$route.query.key
    this.searchNum = this.$options.filters.filterBy(this.result, this.searchText).length
    if (this.searchNum === 0) {
      this.searchEmpty = true
    } else {
      this.searchEmpty = false
    }
  }
}
</script>
<style>
.hd-search-result{max-width: 1200px; min-width: 1000px; margin: 20px auto; position: relative; overflow: hidden;min-height: 800px;font-size: 12px;}
.hd-search-result-title{height: 30px; line-height: 30px; margin-bottom: 20px; background-color: #f9f9f9; padding: 0 10px;}
.hd-search-result-title b{ color: #0a72ac;margin: 0 5px; }
.hd-search-left{ position: absolute;left: 0;top: 50px; height: 100%; width:210px; border-right: 1px solid #ddd; padding:0 5px;  }
.hd-search-right{margin-left: 250px; padding-bottom: 20px;min-height: 800px;}
.hd-search-left h3{ height: 20px;margin-bottom: 10px; color: #666; }
.hd-search-relate{ overflow: hidden; }
.hd-search-item{margin-bottom: 20px;}
.hd-search-item a:hover p{color: #129be7; }
.hd-search-item p{margin-bottom: 5px;}
.hd-search-item-pic img{ width:180px; height: 180px; border: 1px solid #eee; } 
.hd-search-item-price{font-size: 14px; }
.hd-search-item-price b{ color: #da2525;}
.hd-search-item-text{max-height: 51px; color: #999;}
</style>


