<template>
<div class="hd-login">
  <div class="hd-login-logo"><a v-link="{path:'/'}"><img src="../../assets/images/logo.png" alt="logo"></a></div>
  <div class="hd-login-box">
    <h1>Sign in</h1>
    <validator name="validation1" >
      <form  class="hd-login-form" novalidate>
        <div class="hd-login-form-item" >
          <label for="email">Email</label><input type="text" name="" id="email" v-validate:username="['required']">
        </div>
        <div class="hd-login-form-item">
          <label for="password">Password</label><input type="password" name="" id="password" v-validate:password="['url']">
          <a v-link="{path:'/forgot'}" class="forgot">Forgot your password?</a>
        </div>
        <div class="hd-validate-cnt">
            <p class="hd-validate-error" v-show="$validation1.username.required">Required your name.</p>
        </div>
        <div class="hd-login-form-item">
          <button type="button" class="hd-login-form-item-btn" v-link="{path:'/'}">Sign in</button>
        </div>
      </form>
    </validator>
    <div class="hd-login-regist">
      <div class="title">
        <p>-------------- New to HDHawk? --------------</p>
      </div>
      <button type="button" class="hd-login-regist-btn"  v-link="{path:'/register'}">Creat your new account</button>
      <p class="tips">By signing in you are agreeing to our <a href="">Conditions of Use and Sale</a> and our <a href="">Privacy Notice</a>.</p>
    </div>
  </div>
</div>
<div class="hd-login-footer">
  <p class="link"><a v-link="">Conditions of Use</a><a v-link="">Privacy Notice</a></p>
  <p class="copyright">Copyright &copy; 2016 HDHAWK Inc. All right reserved.</p>
</div>
</template>

<script>
export default {
  data () {
    return {
    }
  },
  validators: { // `numeric` and `url` custom validator is local registration
    numeric: function (val) {
      return /^[-+]?[0-9]+$/.test(val)
    },
    url: function (val) {
      return /^(http\:\/\/|https\:\/\/)(.{4,})$/.test(val)
    }
  },
  methods: {
  },
  computed: {
  },
  events: {
  }
}
</script>
<style>
.hd-login{ width: 360px; margin: 50px auto;font-size: 14px; }
.hd-login-logo{text-align: center;margin-bottom: 20px;}
.hd-login-box{border: 1px solid #ddd;border-radius: 5px;padding: 20px;}
.hd-login-box h1{font-weight: 400; color: #333;margin-bottom: 10px;}
.hd-login-form-item{margin-bottom: 15px;position: relative;}
.hd-login-form-item label{ display: block;margin-bottom: 5px; }
.hd-login-form-item input{ padding: 8px 3%;border: 1px solid #ccc; border-radius: 4px; width: 94%; color: #0b72ac;}
.hd-login-form-item .forgot{position: absolute; right: 0; top: 5px; font-size: 11px; color: #999;}
.hd-login-form-item .forgot:hover{ color: #0b72ac; text-decoration: underline; }
.hd-login-form-item-btn{background-color: #0a72ac; color: #fff; height: 35px; width: 100%; border-radius: 5px; border: none; cursor: pointer;margin-top: 10px;}
.hd-login-form-item-btn:hover{background-color: #129be7;}
.hd-login-regist .title{position: relative; height: 30px; line-height: 30px; color: #ccc; font-size: 12px; text-align: center;}
.hd-login-regist-btn{margin-top: 10px; height: 34px; width: 100%; background-color: #f1f1f1;background : linear-gradient(top, #e8eaed, #f5f6f8); border-radius: 5px;border: 1px solid #ccc; color: #666; cursor: pointer;}
.hd-login-regist-btn:hover{background : linear-gradient(top, #e8eaed, #eee);color: #0b72ac;}
.hd-login-regist .tips{ padding: 20px 0; line-height: 20px; color: #666;font-size: 12px; }
.hd-login-regist .tips a{ color: #0b72ac; }
.hd-login-regist .tips a:hover{ text-decoration: underline; color: #129be7; }
.hd-login-footer{ border-top: 1px solid #eee; padding: 20px 0; text-align: center;font-size: 12px;margin-top: 150px;background : linear-gradient(top, #f9f9f9, #fff);}
.hd-login-footer p {margin-bottom: 5px;}
.hd-login-footer p a{ color:#0b72ac;margin: 0 10px;  }
.hd-login-footer p a:hover{text-decoration: underline;}
.hd-login-footer p.copyright{ color: #999; }
</style>


